import {Component, Input, OnDestroy, OnInit, ViewContainerRef} from "@angular/core";
import {TextInputComponentViewModel} from "./TextInputComponentViewModel";
import {BlinkingComponentController} from "./BlinkingComponentController";

@Component({
  selector: 'my-text-input-component[viewModel]',
  templateUrl: './text-input-component.component.html',
  // styleUrls: ['./text-input-component.component.scss']
})
export class TextInputComponentComponent implements OnInit, OnDestroy {
  @Input() viewModel!: TextInputComponentViewModel;
  private blinkController!: BlinkingComponentController;

  constructor(private readonly viewContainerRef: ViewContainerRef) {}

  ngOnInit(): void {
    if(this.viewModel === null) {
      throw new Error("No viewModel");
    } else {
      this.blinkController = new BlinkingComponentController(this.viewContainerRef.element.nativeElement, ".input", this.viewModel);
    }
  }

  ngOnDestroy(): void {
    this.blinkController.destroy();
  }


}
