export class ScreenAuthorizationType {

  constructor(readonly name: string) {}

  static copy(other: ScreenAuthorizationType) {
    return new ScreenAuthorizationType(other.name);
  }

  static user = new ScreenAuthorizationType("user");
  static screen = new ScreenAuthorizationType("screen");

}


export class AutomaticVariableMode {
  constructor(readonly name: string) {}
  static bind = new AutomaticVariableMode("bind");
  static init = new AutomaticVariableMode("init");

  static byName(name: string) {
    switch (name) {
      case AutomaticVariableMode.bind.name: return AutomaticVariableMode.bind;
      case AutomaticVariableMode.init.name: return AutomaticVariableMode.init;
      default: throw new Error("Incorrect mode ["+name+"]");
    }
  }

  isInit() {
    return this.name == AutomaticVariableMode.init.name;
  }

  isBind() {
    return this.name == AutomaticVariableMode.bind.name;
  }
}


export class ScreenLifeMode {
  constructor(readonly name: string) {}

  static volatile = new ScreenLifeMode("volatile");
  static singleInstance = new ScreenLifeMode("singleInstance");
  static multipleInstances = new ScreenLifeMode("multipleInstances");

  static copy(other: ScreenLifeMode) {
    switch (other.name) {
      case ScreenLifeMode.volatile.name: return ScreenLifeMode.volatile;
      case ScreenLifeMode.singleInstance.name: return ScreenLifeMode.singleInstance;
      case ScreenLifeMode.multipleInstances.name: return ScreenLifeMode.multipleInstances;
      default: throw new Error("Unsupported life mode '"+other.name+"'");
    }
  }

  isVolatile() {
    return this.name === ScreenLifeMode.volatile.name;
  }
  isSingleInstance() {
    return this.name === ScreenLifeMode.singleInstance.name;
  }
  isMultipleInstances() {
    return this.name === ScreenLifeMode.multipleInstances.name;
  }
}
