import {
  BackgroundsPropertiesState,
  BordersPropertiesState,
  LabeledScreenComponentState,
  LabelPropertiesState,
  PaddingsPropertiesState,
  PropertiesStateHolder,
  ScreenComponentRefState,
  TextPropertiesState
} from "@screens";
import {ArrayVariable, BusinessVariable} from "@libs/ProcessVariables";
import {MultiCheckboxComponentDefinition} from "./MultiCheckboxComponentDefinition";
import {Option} from "@libs/Option";

export class MultiCheckboxComponentState extends LabeledScreenComponentState {
  static className = "MultiCheckboxComponentState";
  className() {
    return MultiCheckboxComponentState.className;
  }
  readonly textState = new TextPropertiesState(this.properties);
  override readonly labelState = new LabelPropertiesState(this.properties);
  readonly paddingsState = new PaddingsPropertiesState("", this.properties);
  readonly bordersState = new BordersPropertiesState(this.properties);
  readonly backgroundsState = new BackgroundsPropertiesState("", this.properties);

  constructor(readonly properties: PropertiesStateHolder) {
    super(properties);
  }

  get model() {
    return this.properties.optionalArrayProperty(MultiCheckboxComponentDefinition.MODEL);
  }

  get options() {
    return this.properties.labeledValuesProperty("options");
  }

  get entriesLayout() {
    return this.properties.stringProperty("entriesLayout")
  }

  get iconBackgroundColor() {return this.properties.optionalStringProperty("iconBackgroundColor")}

  override updateModel(modelName: string, value: Option<BusinessVariable>) {
    switch (modelName) {
      case MultiCheckboxComponentDefinition.MODEL:
        if(value.isDefined() && value.get().className() === ArrayVariable.className && (<ArrayVariable<BusinessVariable>>value.get()).nonEmpty()) {
          this.properties.putValue(MultiCheckboxComponentDefinition.MODEL, value.get());
        } else if(value.isDefined()) {
          throw Error("Unable to set value, expected Array but was ["+value.get().className()+"]");
        } else {
          this.properties.clearValue(MultiCheckboxComponentDefinition.MODEL);
        }
        break;
      default: throw new Error("Model ["+modelName+"] is not supported by "+this.className());
    }
  }

  static copy(other: MultiCheckboxComponentState) {
    return new MultiCheckboxComponentState(PropertiesStateHolder.copy(other.properties));
  }

}

export class MultiCheckboxComponentRefState extends ScreenComponentRefState {
  static className = "MultiCheckboxComponentRefState";
  className() {
    return MultiCheckboxComponentRefState.className;
  }

  get required() {
    return this.properties.booleanProperty("required");
  }

  constructor(readonly properties: PropertiesStateHolder) {
    super(properties);
  }

  static copy(other: MultiCheckboxComponentRefState) {
    return new MultiCheckboxComponentRefState(PropertiesStateHolder.copy(other.properties));
  }

}
