import {BusinessVariable, ObjectVariable} from "@libs/ProcessVariables";
import {__} from "@libs/FunctionalExtensions";
import {
  ComponentsCommon,
  ComponentViewModelUtils,
  ComponentViewModelWithLabel,
  ScreenContainerViewModel,
  ScreenInstanceServerModel,
  ScreenSharedViewModel
} from "@screens";
import {DropListComponentDefinition, DropListComponentRef} from "./DropListComponentDefinition";
import {DropListComponentRefState, DropListComponentState} from "./DropListComponentState";
import {VariableId} from "@libs/IdModel";
import {None, Option, Some} from "@libs/Option";
import {depolonize, depolonizeLowerCase} from "@libs/Internationalization";
import {i18n} from "../../../shared/internationalization/i18n.service";

export class SelectComponentOption {
  constructor(readonly id: string,
              public selected: boolean,
              readonly label: string,
              readonly value: BusinessVariable | undefined) {}

  static of(index: number, value: BusinessVariable) {
    if(value instanceof ObjectVariable) {
      if(value.valueFor("label").isDefined() && value.valueFor("value")) {
        return new SelectComponentOption(index+"", false, value.valueFor("label").get().valueToSimpleString(), value.valueFor("value").get());
      } else {
        throw new Error("Objects in options must have fields 'value' and 'label'");
      }
    } else {
      return new SelectComponentOption(index+"", false, value.valueToSimpleString(), value);
    }
  }

  static EMPTY(placeholder: string) {
    return new SelectComponentOption("xnp7Y6uPONKZiKEr", false, placeholder, undefined);
  }
}

export class DropListComponentViewModel extends ComponentViewModelWithLabel {

  override typeName = "DropList";


  public notSelected: boolean = true;
  public valueId: string|undefined;
  public valueLabel: string|undefined;
  public valueDefined: boolean = false;
  public tooltip: Option<string> = Option.NoneSingleton;
  public placeholder: string = "";
  public outsideCss: string = "";
  public innerCss: string = "";
  public required: boolean = false

  public options!: Array<SelectComponentOption>;
  public filterVisible: boolean = false;

  // Temporary state
  public filter: string = "";
  public visibleOptions!: Array<SelectComponentOption>;
  public activeIndex = 0;

  public outerShadowCss = "";
  public css = "";


  constructor(override readonly shared: ScreenSharedViewModel,
              override readonly parent: ScreenContainerViewModel|null,
              readonly context: VariableId,
              override readonly definition: DropListComponentDefinition,
              override readonly componentScreenId: string,
              readonly ref: DropListComponentRef,
              override readonly refScreenId: string,
              override readonly componentState: DropListComponentState,
              readonly refState: DropListComponentRefState,
              readonly serverModel: ScreenInstanceServerModel
  ) {
    super(parent, definition, componentState, refState, shared);
    this.update();
  }


  updateComponent(deep: boolean): void {

    this.innerCss =  ComponentViewModelUtils.toPaddingsCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.paddingsProperties, this.componentState.paddingsState) +
      ComponentViewModelUtils.toBorderCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.bordersProperties, this.componentState.bordersState) +
      ComponentViewModelUtils.toBackgroundCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.backgroundsProperties, this.componentState.backgroundsState) +
      ComponentViewModelUtils.toTextCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.textProperties, this.componentState.textState);

    this.placeholder = this.definition.placeholder.currentValue(() => this.componentState.placeholder).valueOrDefault(None()).map(p => p.getCurrentWithFallback()).getOrElse("-- "+i18n("screen_select_select")+" --");

    this.options = [SelectComponentOption.EMPTY(this.placeholder)].concat(this.definition.options.currentValue(() => this.componentState.options).valueOrDefault([]).map((s, index) => SelectComponentOption.of(index, s)));
    this.filterVisible = this.options.length > 5;

    this.prepareValue();
    this.tooltip = this.definition.tooltip.currentValue(() => this.componentState.tooltip).valueOrDefault(None()).map(t => t.getCurrentWithFallback());
    this.required = this.ref.required.currentValue(() => this.refState.required).valueOrDefault(false);

    this.outerShadowCss = ComponentViewModelUtils.toOuterShadowCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.bordersProperties, this.componentState.bordersState);
    const innerShadow = this.definition.innerShadow(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider).currentValue(() => this.componentState.innerShadow).valueOrDefault(None());
    this.innerCss += ComponentsCommon.innerShadowCss(innerShadow);

    console.log("css updated");

    this.updateTemporaryState();
    super.updatePosition();

    this.css = this.innerCss + this.sizeCss;
  }

  private matches(option: SelectComponentOption, filter: string): boolean {
    const trimmedFilter = filter.trim();
    if(trimmedFilter.length == 0) {
      return true;
    } else {

      const normalizedFilter = depolonize(trimmedFilter);
      const normalizedLowercaseFilter = normalizedFilter.toLocaleLowerCase();

      const labelLowercase = depolonizeLowerCase(option.label.trim());
      if(labelLowercase.indexOf(normalizedLowercaseFilter) >= 0) {
        return true;
      } else {
       const splittedWords = normalizedLowercaseFilter.split(/\s/).map(w => w.toLocaleLowerCase());
       if(splittedWords.length > 1) {
         return __(splittedWords).all(w => labelLowercase.indexOf(w) >= 0);
       } else {
         const camelCaseWords = normalizedFilter.split(/(?=[A-Z])/).map(w => w.toLocaleLowerCase());
         return __(camelCaseWords).all(w => labelLowercase.indexOf(w) >= 0);
       }
      }
    }
  }

  updateTemporaryState() {
    this.visibleOptions = this.options.filter(o => this.matches(o, this.filter));
    this.updateActiveIndexToSelected();
  }

  updateActiveIndexToSelected() {
    this.activeIndex = __(this.visibleOptions).findIndexOf(o => o.id == this.valueId).getOrElse(0);
  }

  activeNext(){
    this.activeIndex++;
    if(this.activeIndex >= this.visibleOptions.length){
      this.activeIndex = 0;
    }
  }

  activePrevious(){
    this.activeIndex--;
    if(this.activeIndex < 0){
      this.activeIndex = this.visibleOptions.length - 1;
    }
  }

  selectActive(){
    if(this.visibleOptions.length > this.activeIndex) {
      this.optionSelected(this.visibleOptions[this.activeIndex]);
    }
  }

  filterUpdated() {
    this.updateTemporaryState();
  }

  clearFilter() {
    this.filter = "";
    this.filterUpdated();
  }

  prepareValue(): void {
    const valueOption = this.componentState.model.valueOrDefault(None());
    this.notSelected = valueOption.isEmpty();
    const value = valueOption.getOrUndefined();
    const existing = __(this.options).find(o => o.value == undefined && value == undefined || o.value != undefined && o.value.isEqual(value));

    if(existing.isDefined()) {
      this.valueId = existing.get().id;
      this.valueLabel = existing.get().label;
    } else {
      this.valueId = "-0"; // TODO handle missing option
      this.valueLabel = "???";
    }
    this.valueDefined = valueOption.isDefined();
  }



  optionSelected(option: SelectComponentOption) {
    this.valueId = option.id;
    this.valueLabel = option.label;

    if (option.value === undefined || option.value === null) {
      this.notSelected = true;
      this.componentState.updateModel(DropListComponentDefinition.MODEL, None());
      this.serverModel.clearModelWithAction(this.componentRefPath(), DropListComponentDefinition.MODEL, DropListComponentDefinition.ON_CHANGE);
    } else {
      this.notSelected = false;
      this.componentState.updateModel(DropListComponentDefinition.MODEL, Some(option.value));
      this.serverModel.changeModelWithAction(this.componentRefPath(), DropListComponentDefinition.MODEL, option.value, DropListComponentDefinition.ON_CHANGE);
    }

  }

}
