import {Component, Input, OnDestroy, OnInit, ViewContainerRef} from "@angular/core";
import {
  FileInfoViewModel, RefIdInContext,
  SingleAttachmentInputComponentDefinition,
  SingleAttachmentInputComponentViewModel
} from "@screens";
import {LibrariesService} from "../../../shared/libraries/libraries.service";
import {None, Option, Some} from "@libs/Option";
import {SimpleFileUploaderController} from "@libs/simpleFileUploaderController/SimpleFileUploaderController";
import {UploadFormFileResponse, UploadFormFileSuccess} from "@libs/simpleFileUploaderController/Messages";
import toastr from "@libs/components/toastr/toastr";
import {prettySize} from "@libs/time/Size";
import {__} from "@libs/FunctionalExtensions";

declare const qq: any;
@Component({
  selector: 'my-single-attachment-input-component[viewModel]',
  templateUrl: './single-attachment-input-component.component.html',
  // styleUrls: ['./button-component.component.scss']
})
export class SingleAttachmentInputComponentComponent implements OnInit, OnDestroy {
  @Input() viewModel!: SingleAttachmentInputComponentViewModel;

  maxFileSize = 10 * 1024 * 1024; // TODO Parametrize

  uploadHandlerInitialized: boolean = false;
  dropAreaInitialized = false;
  fileUploadController: Option<SimpleFileUploaderController> = None();
  dragAndDropModule: Option<any> = None();

  constructor(private readonly viewContainerRef: ViewContainerRef,
              private readonly librariesService: LibrariesService) {
  }

  ngOnInit(): void {
    this.librariesService.loadFineUploader(() => {
      setTimeout(() => {
        this.initUploadHandler();

        if(!this.dropAreaInitialized) {
          const element = <HTMLElement>this.viewContainerRef.element.nativeElement;
          const uploadDisplay = element.querySelector(".fileUploadDisplay")
          this.dragAndDropModule.forEach(d => d.setupExtraDropzone(uploadDisplay));
          this.dropAreaInitialized = true;
        }
      });

    });
  }


  initUploadHandler() {

    this.uploadHandlerInitialized = true;

    const uploadButtonSelector: HTMLElement = <HTMLElement>(<HTMLElement>this.viewContainerRef.element.nativeElement).querySelector(".uploadButton");

    if(uploadButtonSelector === null) {
      throw new Error("No uploadButton")
    } else {

      this.fileUploadController = Some(new SimpleFileUploaderController(
        'screen/add-file-to-model',
              uploadButtonSelector,
        "" /*organizationSessionInfo.sessionToken*/, this.viewModel.allowedExtensions, this.maxFileSize,
              true,
              (id: number, name: string, responseJSON: UploadFormFileResponse) => this.onFileUploadComplete(id, name, UploadFormFileSuccess.copy(<UploadFormFileSuccess>responseJSON)),
              (id, fileName, instance) => this.onFileUpload(id, fileName, instance),
              (file: {name: string, size: number}): boolean => this.onFileUploadValidate(file),
              (id: any, name: any, reason: any, request: any) => this.onFileUploadError(id, name, reason, request),
              (id: number, name: string, uploadedBytes: number, totalBytes: number) => this.onFileUploadProgress(id, name, uploadedBytes, totalBytes)));


      this.dragAndDropModule = Some(new qq.DragAndDrop({
        allowMultipleItems: true,
        dropZoneElements: [],
        classes: {
          dropActive: "fileDragged"
        },
        callbacks: {
          processingDroppedFiles: () => {
            this.hideDropZone();
          },
          processingDroppedFilesComplete: (files: string | any[], dropTarget: any) => {
            // 0 elements can appear for files with strange names on Chrome - dnd module of fine uploader uses
            // webkitGetAsEntry method which does not work for strange file names
            this.hideDropZone();
            if(this.viewModel.editable && !this.viewModel.attachmentDefined) {
              if (files.length == 1) {
                this.fileUploadController.forEach(c => c.fineUploaderBasicInstance.addFiles(files)); //this submits the dropped files to Fine Uploader
              } else if(files.length > 1) {
                toastr.error("Multiple files not allowed");
              } else {
                toastr.error("File name not allowed");
              }
            }
          }
        }
      }));

    }


    this.initListeners();
  }

    private initListeners() {
      document.addEventListener("dragover", this.onDragOver);
      document.addEventListener("dragleave", this.onDragLeave);
      document.addEventListener("drop", this.onDrop);
    }


    ngOnDestroy(): void {
      document.removeEventListener("dragover", this.onDragOver);
      document.removeEventListener("dragleave", this.onDragLeave);
      document.removeEventListener("drop", this.onDrop);
    }

    onDragOver = (e: Event) => {
      if (this.viewModel.editable) {
        const dt = (<DragEvent>e).dataTransfer;
        if (dt !== null && dt.types && (dt.types.indexOf ? dt.types.indexOf('Files') != -1 : __(<Array<string>>dt.types).contains('Files'))) {
          this.showDropZone()
        }
      }
    };

    onDragLeave = (e: Event) => {
      if ((<DragEvent>e).relatedTarget == null) { //moves outside window, otherwise it's other elementthat can handle drop
        this.hideDropZone();
      }
    };


    onDrop = (e: Event) => {
      this.hideDropZone();
    };

    showDropZone() {
      const element = <HTMLElement>this.viewContainerRef.element.nativeElement;
      const uploadDisplay = element.querySelector(".fileUploadDisplay")

      if(uploadDisplay !== null) {
        uploadDisplay.classList.add("fileDropZone");
      }
    }

    hideDropZone() {
      const element = <HTMLElement>this.viewContainerRef.element.nativeElement;
      const uploadDisplay = element.querySelector(".fileUploadDisplay")
      if(uploadDisplay !== null) {
        uploadDisplay.classList.remove("fileDropZone");
        uploadDisplay.classList.remove("fileDragged");
      }
    }

      onFileUploadComplete(uploadId: number, name: string, responseJSON: UploadFormFileSuccess){
        this.hideDropZone();
        this.viewModel.fileUploadCompleted(uploadId, responseJSON.fileUri);
      }

      onFileUploadProgress(uploadId: number, name: string, uploadedBytes: number, totalBytes: number){
        this.hideDropZone();
        this.viewModel.fileUploadProgress(uploadId, uploadedBytes, totalBytes);
      }

      onFileUploadError(uploadId: number, name: string, reason: string, request: any){
        this.hideDropZone();
        toastr.error(reason);
        this.viewModel.fileUploadError(uploadId);
        console.log(reason); // seems to be called for correct upload
      }

      onFileUploadValidate(file: {name: string, size: number}){
        this.hideDropZone();

        if(!this.viewModel.editable) {
          toastr.error("You are not allowed to upload to read only field");
          return false;
        } else if(file.size == 0) {
          toastr.error("You are not allowed to upload empty file");
          return false;
        } else if(file.size > this.maxFileSize) {
          toastr.error("File is too large, max allowed file size is "+prettySize(this.maxFileSize));
          return false;
        } else if(!this.viewModel.extensionValid(file.name)){
          toastr.error("File type not allowed for file "+file.name+", allowed: "+this.viewModel.allowedExtensions.join(", "));
          return false;
        } else {
          return true;
        }
      }


      onFileUpload(id: number, fileName: string, fineUploaderBasicInstance: any){

        this.hideDropZone();

        // TODO for now versioning is disabled
        // if(previousFile.isDefined()) {
        //   fineUploaderBasicInstance.setParams({
        //     aggregateId: taskInfo .flowId.id,
        //     taskId: taskInfo.taskId.id,
        //     flowCursorVersion: taskInfo.cursorVersion,
        //     sectionId: scope.viewModel.sectionId.id,
        //     fieldId: scope.viewModel.elementId.id,
        //     fileUri: previousFile.get().uri.serialize(),
        //     version: previousFile.get().version
        //   });
        // } else {
        fineUploaderBasicInstance.setParams({
          mode: "put",
          instanceId: this.viewModel.instanceId.id,
          refsPath: this.serializeRefsPath(this.viewModel.componentRefPath()),
          modelName: SingleAttachmentInputComponentDefinition.MODEL,
        });
        // }

        this.viewModel.newFileUploadStarted(id, fileName);
      }

      serializeRefsPath(path: Array<RefIdInContext>): string {
        return path.map(r => r.refId.screenId+"_"+r.refId.id+"_"+r.contextId.id).join(";");
      }

      cancelUpload = (fileInfo: FileInfoViewModel) => {
        this.hideDropZone();
        this.fileUploadController.forEach(c => c.cancelUpload(fileInfo.uploadId));
        this.viewModel.onCancel();
      };
}
