import {Pipe, PipeTransform} from "@angular/core";
import {DomSanitizer, SafeStyle} from "@angular/platform-browser";

@Pipe({name: 'sanitizeStyle'})
export class SanitizeStylePipe implements PipeTransform {

  constructor(private readonly sanitizer: DomSanitizer) {
  }

  transform(input: string|null): SafeStyle {
    if (input === null) {
      console.log("Sanitizing not", input);
      return this.sanitizer.bypassSecurityTrustStyle("");
    } else {
      console.log("Sanitizing", input, this.sanitizer.bypassSecurityTrustHtml(input));
      return this.sanitizer.bypassSecurityTrustHtml(input);
    }
  }

}
