import {
  ComponentViewModelUtils, ComponentViewModelWithLabel,
  ScreenContainerViewModel
} from "../ScreenComponentViewModel";
import {None, Option, Some} from "@libs/Option";
import {LocalDate} from "@libs/Types";
import {ScreenSharedViewModel} from "../../";
import {VariableId} from "@libs/IdModel";
import {DateInputComponentDefinition, DateInputComponentRef} from "./DateInputComponentDefinition";
import {DateInputComponentRefState, DateInputComponentState} from "./DateInputComponentState";
import {ScreenInstanceServerModel} from "../../ScreenInstanceServerModel";
import {DateVariable} from "@libs/ProcessVariables";
import {I18nText} from "@libs/I18nText";
import {ComponentsCommon} from "../ComponentsModel";
import {range} from "@libs/Array";
import {parseDateInputString} from "../../../../../../../utils/time/Parsers";
import {toFixedLengthString} from "@libs/Strings";


export class CalendarDay {
  label: string;
  value: LocalDate;
  otherMonth: boolean;

  constructor(label: string, value: LocalDate, otherMonth: boolean) {
    this.label = label;
    this.value = value;
    this.otherMonth = otherMonth;
  }
}

export class CalendarWeek {
  weekNr: number;
  days: Array<CalendarDay>;

  constructor(weekNr: number, days: Array<CalendarDay>) {
    this.weekNr = weekNr;
    this.days = days;
  }
}

export class DateInputPicker {
  weeks: Array<CalendarWeek> = [];
  private tempDate: LocalDate = LocalDate.nowDate();

  constructor() {
    this.prepareCalendar();
  }

  getTempDate() {
    return this.tempDate;
  }

  setTempDate(other: LocalDate) {
    this.tempDate = other;
    this.prepareCalendar();
  }

  setPreviousMonth() {
    if(this.tempDate.month > 1) {
      this.setTempDate(new LocalDate(this.tempDate.year, this.tempDate.month - 1, 1));
    } else {
      this.setTempDate(new LocalDate(this.tempDate.year - 1, 12, 1));
    }
  }

  setNextMonth() {
    if(this.tempDate.month < 12) {
      this.setTempDate(new LocalDate(this.tempDate.year, this.tempDate.month + 1, 1));
    } else {
      this.setTempDate(new LocalDate(this.tempDate.year + 1, 1, 1));
    }
  }

  prepareCalendar() {
    const year = this.tempDate.year;
    const month = this.tempDate.month;
    const weeks: Array<CalendarWeek> = [];

    let currentIndex = 1;
    let endIndex = LocalDate.daysInMonth(this.tempDate);

    let endWeekDay = 0 // sunday
    let daysInWeek = [];
    let weekNr = 1;
    while (currentIndex <= endIndex) {
      let currentDate = new LocalDate(year, month, currentIndex);
      daysInWeek.push(new CalendarDay(currentIndex.toString(), currentDate, false));

      if(currentDate.weekDay() === endWeekDay || currentIndex === endIndex) {
        weeks.push(new CalendarWeek(weekNr, daysInWeek));
        daysInWeek = [];
        weekNr += 1;
      }
      currentIndex += 1;
    }

    if(weeks[0].days.length < 7) {
      const firstDate = weeks[0].days[0].value;
      range(7-weeks[0].days.length).forEach(nr => {
        const prevDate = firstDate.addDays(-nr - 1);
        weeks[0].days.unshift(new CalendarDay(prevDate.day.toString(), prevDate, true));
      })
    }

    if(weeks[weeks.length-1].days.length < 7) {
      const lastDate = weeks[weeks.length-1].days[weeks[weeks.length-1].days.length - 1].value;
      range(7-weeks[weeks.length-1].days.length).forEach(nr => {
        const nextDate = lastDate.addDays(nr + 1);
        weeks[weeks.length-1].days.push(new CalendarDay(nextDate.day.toString(), nextDate, true));
      })
    }

    this.weeks = weeks;
  }

}

export class DateInputComponentViewModel extends ComponentViewModelWithLabel {

  override typeName = "DateInput";

  public value: Option<LocalDate> = Option.NoneSingleton;
  public model: string|undefined;
  public readableModel: string|undefined;

  public tooltip: Option<string> = Option.NoneSingleton;
  public placeholder: string = "";
  public outsideCss: string = "";
  public innerCss: string = "";
  public required: boolean = true;

  public pickerVisible: boolean = false;
  public anchorVisible: boolean = false;

  public combinedCss = "";

  public minValue: Option<LocalDate> = Option.NoneSingleton;
  public maxValue: Option<LocalDate> = Option.NoneSingleton;

  public dateInputPicker: DateInputPicker = new DateInputPicker();

  constructor(override readonly shared: ScreenSharedViewModel,
              override readonly parent: ScreenContainerViewModel|null,
              readonly context: VariableId,
              override readonly definition: DateInputComponentDefinition,
              override readonly componentScreenId: string,
              readonly ref: DateInputComponentRef,
              override readonly refScreenId: string,
              override readonly componentState: DateInputComponentState,
              readonly refState: DateInputComponentRefState,
              readonly serverModel: ScreenInstanceServerModel
  ) {
    super(parent, definition, componentState, refState, shared);
    this.update();
  }


  private isEnabled(){
    return this.editable;
  }

  onClick = () => {
    if(this.isEnabled()) {
      this.openPicker();
    }
  }

  openPicker() {
    if(this.value.isDefined()) {
      this.dateInputPicker.setTempDate(this.value.get());
    }
    this.pickerVisible = true;
  }

  setValue(value: LocalDate) {
    this.value.value = value;
    this.updateModel();
    this.closePicker();
  }

  parseModel() {
    if (!this.model || this.model.trim().length == 0) return;
    const date = parseDateInputString(this.model);
    this.model = toFixedLengthString(date.getDate(), 2)
      + '-' + toFixedLengthString(date.getMonth() + 1, 2)
      + '-' + toFixedLengthString(date.getFullYear(), 4);
  }

  isSelected(dateSelected: LocalDate) {
    return this.value.isDefined() && this.value.get().isEqual(dateSelected);
  }

  updateModel() {
    const date = this.value.get().toDate();
    this.model = toFixedLengthString(date.getDate(), 2)
      + '-' + toFixedLengthString(date.getMonth() + 1, 2)
      + '-' + toFixedLengthString(date.getFullYear(), 4);
  }

  closePicker = () => {
    this.pickerVisible = false;
    this.onChange();

    const correct = LocalDate.of(this.model).isSuccess();
    if (!correct) {
      this.model = undefined;
      this.readableModel = undefined;
    }
    this.updateValueChanged();
    if (correct) {
      this.readableModel = this.value.map(f => f.formattedShortWords()).getOrUndefined();
    }
  }

  onChange = () => {
    if (this.model == undefined || this.model.trim().length === 0) {
      this.model = undefined;
      this.readableModel = undefined;
    }
  };

  onBlur = () => {
    this.parseModel();
    this.closePicker();
  };

  setEmpty = () => {
    this.model = undefined;
    this.readableModel = undefined;
    this.closePicker();
  };

  setToday = () => {
    const nowDate = LocalDate.nowDate();
    this.model = nowDate.formatted();
    this.readableModel = nowDate.formattedShortWords();
    this.closePicker();
  };

  setYesterday = () => {
    const yesterdayDate = new Date();
    yesterdayDate.setDate(yesterdayDate.getDate() - 1);
    const yesterday = LocalDate.fromDate(yesterdayDate);
    this.model = yesterday.formatted();
    this.readableModel = yesterday.formattedShortWords();
    this.closePicker();
  };

  getCurrentValue(): Option<LocalDate> {
    if(this.model && (<string>this.model).trim().length > 0) {
      const date = LocalDate.of(this.model);
      if(date.isSuccess()) {
        return Some(date.result);
      }
    }
    return Option.NoneSingleton;
  };

  updateValueChanged() {
    if(this.editable) {
      this.value = this.getCurrentValue();

      if(this.value.isEmpty()) {
        this.componentState.updateModel(DateInputComponentDefinition.MODEL, None());
        this.serverModel.clearModelWithAction(this.componentRefPath(), DateInputComponentDefinition.MODEL, DateInputComponentDefinition.ON_CHANGE);
      } else {
        this.componentState.updateModel(DateInputComponentDefinition.MODEL, Some(new DateVariable(this.value.get())));
        this.serverModel.changeModelWithAction(this.componentRefPath(), DateInputComponentDefinition.MODEL, new DateVariable(this.value.get()), DateInputComponentDefinition.ON_CHANGE);
      }

    }
  }

  updateComponent(deep: boolean): void {

    this.outsideCss = ComponentViewModelUtils.toSizeCss(this.definition.sizeProperties, this.componentState.boxState) +
      ComponentViewModelUtils.toOuterShadowCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.bordersProperties, this.componentState.bordersState);


    this.innerCss = ComponentViewModelUtils.toBorderCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.bordersProperties, this.componentState.bordersState) +
      ComponentViewModelUtils.toBackgroundCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.backgroundsProperties, this.componentState.backgroundsState) +
      ComponentViewModelUtils.toTextCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.textProperties, this.componentState.textState);

    this.tooltip = this.definition.tooltip.currentValue(() => this.componentState.tooltip).valueOrDefault(Option.NoneSingleton).map(t => t.getCurrentWithFallback());
    this.placeholder = this.definition.placeholder.currentValue(() => this.componentState.placeholder).valueOrDefault(Option.NoneSingleton).getOrElse(I18nText.ofCurrent("dd-mm-yyyy")).getCurrentWithFallback();
    this.required = this.ref.required.currentValue(() => this.refState.required).valueOrDefault(false);

    this.minValue = this.definition.minValue.currentValue(() => this.componentState.minValue).valueOrDefault(Option.NoneSingleton);
    this.maxValue = this.definition.maxValue.currentValue(() => this.componentState.maxValue).valueOrDefault(Option.NoneSingleton);

    this.anchorVisible = this.definition.anchorVisible.currentValue(() => this.componentState.anchorVisible).valueOrDefault(false);

    this.value = this.componentState.model.valueOrDefault(Option.NoneSingleton);
    this.model = this.value.map(d => d.formatted()).getOrUndefined();
    this.readableModel = this.value.map(d => d.formattedShortWords()).getOrUndefined();

    const innerShadow = this.definition.innerShadow(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider).currentValue(() => this.componentState.innerShadow).valueOrDefault(Option.NoneSingleton);
    this.innerCss += ComponentsCommon.innerShadowCss(innerShadow);

    super.updatePosition();

    this.combinedCss = this.outsideCss + this.sizeCss;
  }

}
