
import {Injectable} from '@angular/core';
import {HttpClient, HttpResponse} from '@angular/common/http'
import { Observable } from 'rxjs';
import {restUrl} from "../environment";
import {i18n} from "../shared/internationalization/i18n.service";
  /**
   * Service that provides REST interface to Server Side of the application.
   * It add credential information to request and can handle Unauthorized message.
   * It should be used instead of plain $http service when user authentication is needed.
   */
   @Injectable({
    providedIn: 'root',
  })
  export class AuthenticatedHttp {


    constructor(private http: HttpClient) {}

    private handleRequest(url: string, request: any|null,
                      onSuccess: (data: any, status: any, headers: any) => void,
                      onError: (data: any, status: any, headers: any, xhrStatus: string) => void,
                      secondTry: boolean = false, binary: boolean = false) {

      // this.sessionServicePromise.then(sessionService => {
      //   const sessionToken = sessionService.sessionToken;

        const sessionToken = ""

        const promise: Observable<HttpResponse<any>> = request === null
                       ? <any>this.http.get(restUrl(url), this.createAuthorizationHeaderConfig(sessionToken, binary))
                      : <any>this.http.post(restUrl(url), request, this.createAuthorizationHeaderConfig(sessionToken, binary));


          // ? this.$http.get(appConfig.restUrl(restUrl), this.createAuthorizationHeaderConfig(sessionService.sessionToken, binary))
          // : this.$http.post(appConfig.restUrl(restUrl), request, this.createAuthorizationHeaderConfig(sessionService.sessionToken, binary));


          promise.subscribe({
              next: (result: HttpResponse<any>) => {
                console.log("result");
                onSuccess(result.body, result.status, result.headers);
              },
              error: (reason) => {
                if(reason.status === 401) {
                  if(secondTry) {
                    onError(i18n("errors_not_authenticated"), reason.status, reason.headers, reason.xhrStatus);
                  } else {
                    // sessionService.isAuthenticated(authenticated => {

                    //   const sessionNotChangedMeanwhile = (sessionToken === sessionService.sessionToken);
                    //   if (sessionNotChangedMeanwhile) {
                    //     sessionService.loginToOrganizationAutomatically(() => {
                    //       this.handleRequest(restUrl, request, onSuccess, onError, true);
                    //     }, () => {
                    //       onError(i18n("errors_not_authenticated"), reason.status, reason.headers, reason.config, reason.xhrStatus);
                    //     });
                    //   } else {
                    //     if (authenticated) {
                    //       this.handleRequest(restUrl, request, onSuccess, onError, true);
                    //     } else {
                    //       onError(i18n("errors_not_authenticated"), reason.status, reason.headers, reason.config, reason.xhrStatus);
                    //     }
                    //   }
                    // });

                  }

                } else {
                  if(onError) {
                    onError(reason.data, reason.status, reason.headers, reason.xhrStatus);
                  } else {
                    throw new Error("Not implemnted");
                    // rest.handleError(reason);
                  }
                }
              }

          });
    }


    // private redirectToLoginPage(sessionService) {
    //   sessionService.clearOrganizationSession();
    //   sessionService.clearApplicationSession();

    //   if (this.$location.path() !== "/login") {
    //     sessionService.setLastRequestedUrl(this.$location.path());
    //   }

    //   if (this.$location.path().indexOf("/admin") !== 0) {
    //     this.$location.url("/login");
    //   } else {
    //     this.$location.url("/admin/login");
    //   }
    // }

    get<T>(restUrl: string, onSuccess: (data: T) => void, onError: () => void = () => {}) {
      this.handleRequest(restUrl, null,
        (data: any, status: any, headers: any) => onSuccess(data),
        (data: any, status: any, headers: any, xhrStatus: string) => onError());
    }

    getWithDetailedError<T>(restUrl: string, onSuccess: (data: T) => void, onError: (data: any, status: any, headers: any, xhrStatus: string) => void = () => {}) {
      this.handleRequest(restUrl, null,
        (data: any, status: any, headers: any) => onSuccess(data),
        (data: any, status: any, headers: any, xhrStatus: string) => onError(data, status, headers, xhrStatus));
    }

    // getPromise<T>(restUrl: string): IPromise<T> {
    //   const deferred = this.$q.defer();
    //   this.handleRequest(restUrl, null,(data: any, status:any, headers:any, config:any) => {
    //     deferred.resolve({ data: data, status: status, headers: headers, config: config});
    //   }, (data:any, status:any, headers:any, config:any, xhrStatus: string) => {
    //     deferred.reject({ data: data, status: status, headers: headers, config: config, xhrStatus: xhrStatus});
    //   });
    //   return deferred.promise;
    // }



    post(restUrl: string, requestData: any, onSuccess: (data: any) => void, onError: (status: number, data: any) => void = () => {}, secondTry: boolean = false, binary: boolean = false) {
      this.handleRequest(restUrl, requestData,
        (data: any, status: any, headers: any) => onSuccess(data),
        (data: any, status: any, headers: any, xhrStatus: string) => onError(status, data),
        secondTry, binary);
    }

    // postPromise<T>(restUrl: string, requestData: any): IPromise<T> {
    //   const deferred = this.$q.defer();
    //   this.handleRequest(restUrl, requestData,(data: any, status:any, headers:any, config:any) => {
    //     deferred.resolve({ data: data, status: status, headers: headers, config: config});
    //   }, (data:any, status:any, headers:any, config:any, xhrStatus: string) => {
    //     deferred.reject({ data: data, status: status, headers: headers, config: config, xhrStatus: xhrStatus});
    //   });

    //   return deferred.promise;
    // }

    createAuthorizationHeaderConfig(sessionToken: string, binary: boolean = false): any {
      if(sessionToken.length === 0) {
        return {
          observe: "response",
          responseType: binary ? "arraybuffer": "json",
          withCredentials: true
        };

      } else {
        return {
          observe: "response",
          headers: {
            'Authorization': sessionToken // if this causes NPE then session initialization needs to be tweaked
          },
          responseType: binary ? "arraybuffer": "json",
          withCredentials: true
        };
      }
    }

  }
