import {AggregateId, AggregateVersion} from "./AggregateId";
import {Typed} from "./Typed";

export class AggregateWrapper<T> {

    id: AggregateId;
    version: AggregateVersion;
    aggregate: T;

    constructor(id:AggregateId, version:AggregateVersion, aggregate:T) {
      this.id = id;
      this.version = version;
      this.aggregate = aggregate;
    }

    static copy<T>(other: AggregateWrapper<T>, aggregateCopier: (aggreage: T) => T): AggregateWrapper<T> {
      return new AggregateWrapper(other.id, other.version, aggregateCopier(other.aggregate));
    }
  }


  export class TypedAggregateWrapper<T extends {className():string}> {

    id:AggregateId;
    version:AggregateVersion;
    aggregate:Typed<T>;

    constructor(id:AggregateId, version:AggregateVersion, aggregate:Typed<T>) {
      this.id = id;
      this.version = version;
      this.aggregate = aggregate;
    }

    static copy<T extends {className():string}>(other:TypedAggregateWrapper<T>, copyAggregate:(aggregate: Typed<T>) => Typed<T>) {
      return new TypedAggregateWrapper(other.id, other.version, copyAggregate(other.aggregate));
    }
  }
