import {None, Option, Some} from "@libs/Option";
import {EntriesLayoutType, LayoutType, TextAlign, TextVerticalAlign} from "@screens";

export class CssUtils {

    static isColor(cssColor: string): boolean {
      return cssColor.match(/^#([a-fA-F0-9]{3,4}){1,2}$/) !== null;
    }

    private static cssProp(propName: string, propValue: Option<string|number>): string {
      return propValue.map(v => propName + ": " + v + ";").getOrElse("");
    }

    static childrenPlainMinHeight(contentMinHeightValue: Option<string>, headerMinHeightValue: Option<string>, headerEnabled: boolean, childrenPanelDeltaHeight: Option<string>){
      const defaultHeaderHeightValue = headerEnabled ? "3rem" : "0rem";
      const headerHeightValue = headerEnabled ? headerMinHeightValue.getOrElse(defaultHeaderHeightValue) : defaultHeaderHeightValue;
      const value = contentMinHeightValue.getOrElse("3rem")
        + " - " +  headerHeightValue
        + " + " + childrenPanelDeltaHeight.getOrElse("0rem");
      return this.cssProp('min-height', Some(value).map(v => `calc(${v})`));
    }

    static desiredHeightValue(desiredHeight: Option<string>, contentMinHeight: Option<string>, defaultValue: string): number {
      return parseFloat(desiredHeight.orElse(contentMinHeight).getOrElse(defaultValue));
    }

    static flexGrow(desiredWidth: Option<number>, contentMinWidth: Option<number>): string {
      return this.cssProp("flex-grow", desiredWidth.orElse(contentMinWidth).orElse(Some(1)));
    }

    static flexBasis(parentLayout: LayoutType, desiredWidth: Option<string>, contentMinWidth: Option<string>, desiredHeight: Option<string>, contentMinHeight: Option<string>) {
      if (parentLayout.isHorizontal()) {
        return this.cssProp("flex-basis", desiredWidth.orElse(contentMinWidth));
      } else if (parentLayout.isVertical()) {
        return this.cssProp("flex-basis", desiredHeight.orElse(contentMinHeight));
      }
      return "";
    }

    static internalMinSizes(parentLayout: LayoutType, desiredWidth: Option<string>, contentMinWidth: Option<string>, desiredHeight: Option<string>, contentMinHeight: Option<string>) {
      let internalMinSizes = "";
      if(parentLayout.isStatic()) {
        internalMinSizes += this.cssProp("min-width", desiredWidth.orElse(contentMinWidth));
        internalMinSizes += this.cssProp("min-height", desiredHeight.orElse(contentMinHeight));
      } else if(parentLayout.isHorizontal() || parentLayout.isVertical()) {
        internalMinSizes += this.cssProp("min-height", desiredHeight.orElse(contentMinHeight));
      }
      return internalMinSizes;
    }

    static maxSizes(maxWidthValue: Option<string|number>, maxHeightValue: Option<string|number>): string {
      const maxWidth = this.cssProp("max-width", maxWidthValue);
      const maxHeight = this.cssProp("max-height", maxHeightValue);
      return maxWidth + maxHeight;
    }

    static minSizes(minWidthValue: Option<string|number>, minHeightValue: Option<string|number>): string {
      const minWidth = this.cssProp("min-width", minWidthValue);
      const minHeight = this.cssProp("min-height", minHeightValue);
      return minWidth + minHeight;
    }

    static margins(marginTop: Option<string>, marginRight: Option<string>, marginBottom: Option<string>, marginLeft: Option<string>) {
      let margins = "";
      margins +=  this.cssProp("margin-top", marginTop);
      margins += this.cssProp("margin-right", marginRight);
      margins += this.cssProp("margin-bottom", marginBottom);
      margins += this.cssProp("margin-left", marginLeft);
      return margins;
    }

    static paddings(paddingTop: Option<string>, paddingRight: Option<string>, paddingBottom: Option<string>, paddingLeft: Option<string>) {
      let paddings = "";
      paddings += this.cssProp('padding-top', paddingTop);
      paddings += this.cssProp('padding-right', paddingRight);
      paddings += this.cssProp('padding-bottom', paddingBottom);
      paddings += this.cssProp('padding-left', paddingLeft);
      return paddings;
    }

    static staticPosition(top: Option<string>, right: Option<string>, bottom: Option<string>, left: Option<string>){
      let position = "";
      position += this.cssProp("top", top);
      position += this.cssProp("right", right);
      position += this.cssProp("bottom", bottom);
      position += this.cssProp("left", left);
      return position;
    }

    static gridPosition(gridRowStart: Option<number>, gridRowEnd: number, gridColumnStart: Option<number>, gridColumnEnd:number) {
      let gridPosition = "";
      gridPosition += this.cssProp('grid-row-start', gridRowStart.map(toString).orElse(Some('auto')));
      gridPosition += this.cssProp('grid-row-end', Some(gridRowEnd).orElse(Some(1)).map(v => 'span ' + v));
      gridPosition += this.cssProp('grid-column-start', gridColumnStart.map(toString).orElse(Some('auto')));
      gridPosition += this.cssProp('grid-column-end', Some(gridColumnEnd).orElse(Some(1)).map(v => 'span ' + v));
      return gridPosition;
    }

    static borders(borderTopWidth: Option<string>, borderRightWidth: Option<string>, borderBottomWidth: Option<string>, borderLeftWidth: Option<string>,
                   borderTopColor: Option<string>, borderRightColor: Option<string>, borderBottomColor: Option<string>, borderLeftColor: Option<string>,
                   borderRadius: Option<string>) {
      let widths = "";
      widths += this.cssProp('border-top-width', borderTopWidth);
      widths += this.cssProp('border-right-width', borderRightWidth);
      widths += this.cssProp('border-bottom-width', borderBottomWidth);
      widths += this.cssProp('border-left-width', borderLeftWidth);

      let colors = "";
      colors += this.cssProp('border-top-color', borderTopColor);
      colors += this.cssProp('border-right-color', borderRightColor);
      colors += this.cssProp('border-bottom-color', borderBottomColor);
      colors += this.cssProp('border-left-color', borderLeftColor);

      const borderStyle = (widths.length > 0 || colors.length > 0)? Some('solid'): None();
      const style = this.cssProp('border-style', borderStyle);

      let radius = this.cssProp('border-radius', borderRadius)

      return widths + colors + style + radius;

    }

    static shadows(outerShadow: Option<string>, innerShadow: Option<string>) {
      innerShadow.map(v => "inset " + v);
      if(outerShadow.isDefined() && innerShadow.isDefined()) {
        return this.cssProp('box-shadow', Some(outerShadow.get() + ", " + innerShadow.get()));
      }
      return this.cssProp('box-shadow', outerShadow.orElse(innerShadow));
    }


    static sectionContentOverflow(componentMaxWidth: Option<string>, componentMaxHeight: Option<string>){
      let overflow = "";
      // If content there is max size value on axis set overflow to auto on that axis
      overflow += this.cssProp('overflow-x', componentMaxWidth.map(v => 'auto'));
      overflow += this.cssProp('overflow-y', componentMaxHeight.map(v => 'auto'));
      return overflow;
    }

    static backgroundColor(color: Option<string>) {
      return this.cssProp('background-color', color);
    }

    static gaps(rowGap:Option<string>, columnGap: Option<any>) {
      let gaps = "";
      gaps += this.cssProp('row-gap', rowGap);
      gaps += this.cssProp('column-gap', columnGap);
      return gaps;
    }

    static font(fontFamily: Option<string>, fontSize: Option<string>, bold: boolean, italic: boolean, underlined: boolean, lineThrough: boolean, color: Option<string>) {
      const fontWeight = bold ? Some("bold") : None();
      const fontStyle = italic ? Some("italic") : None();
      let decoration = "";
      if(underlined){ decoration += "underline " }
      if(lineThrough){ decoration += "line-through" }
      decoration = decoration.trim();
      const textDecoration = decoration.length > 0 ? Some(decoration) : None();

      let font = "";
      font += this.cssProp('font-family', fontFamily);
      font += this.cssProp('font-size', fontSize);
      font += this.cssProp('font-weight', fontWeight);
      font += this.cssProp('font-style', fontStyle);
      font += this.cssProp('text-decoration', textDecoration);
      font += this.cssProp('color', color);
      return font;
    }

    static childrenPlainMinWidth(contentMinWidth: Option<string>) {
      return this.cssProp('min-width', contentMinWidth);
    }



    static verticalAlign(align: string) {
      switch(align) {
        case TextVerticalAlign.top.name: return "align-items: start;";
        case TextVerticalAlign.center.name: return "align-items: center;";
        case TextVerticalAlign.bottom.name: return "align-items: end;";
        default: throw new Error("Unsupported vertical align value ["+align+"]");
      }
    }

    static horizontalAlign(align: string) {
      switch(align) {
        case "left": return "justify-content: left; text-align: left;"; // deprecated
        case TextAlign.start.name: return "justify-content: left; text-align: left;";
        case TextAlign.center.name: return "justify-content: center; text-align: center;";
        case "right": return "justify-content: right; text-align: right;";  // deprecated
        case TextAlign.end.name: return "justify-content: right; text-align: right;";
        case TextAlign.justify.name: return "justify-content: center; text-align: justify;";
        default: throw new Error("Unsupported align value ["+align+"]");
      }
    }

    static entriesMaxSizes(maxWidthValue: Option<string>, maxHeightValue: Option<string>, headerHeightValue: Option<string>, headerEnabled: boolean) {
      const headerHeight = headerEnabled ? headerHeightValue : None();
      const maxHeightCalc = maxHeightValue.map(v => v + " - " + headerHeight.getOrElse("0rem"));

      let maxSizes = "";
      maxSizes += this.cssProp('max-width', maxWidthValue);
      maxSizes += this.cssProp('max-height', maxHeightCalc.map(v => `calc(${v})`));
      return maxSizes;
    }

    static repeatableEntryLayout(innerLayout: LayoutType, entryLayout: EntriesLayoutType, minWidthValue: Option<string>, minHeightValue: Option<string>, childrenPanelDeltaHeightValue: Option<string>) {
      const entryWidth = this.cssProp('min-width', minWidthValue);

      const entryHeight = innerLayout.isStatic() ?
        this.cssProp('min-height', minHeightValue.map(v => `calc(${v} + ${childrenPanelDeltaHeightValue.getOrElse('0rem')})`)) :
        this.cssProp('min-height', minHeightValue);

      let css = "";
      if(entryLayout.isHorizontal()){
        css += entryWidth + entryHeight + 'display: inline-block';
      } else if (entryLayout.isVertical()){
        css += entryHeight + 'display: block';
      }

      return css;
    }

    static columnLabelWidth(minWidth:Option<string>, maxWidth: Option<string>) {
      let css = "";
      css += this.cssProp('min-width', minWidth.orElse(Some('3rem')));
      css += this.cssProp('max-width', maxWidth);
      return css;
    }

  }
