import {ToastrService} from "./ToastrService";

export function error(text: string, tag: string = ""): void {
  ToastrService.getInstance().error(text, tag);
}

export function info(text: string, tag: string = ""): void {
  ToastrService.getInstance().info(text, tag);
}

export function warning(text: string, tag: string = ""): void {
  ToastrService.getInstance().warning(text, tag);
}

export function success(text: string, tag: string = ""): void {
  ToastrService.getInstance().success(text, tag);
}

export function clearOldMessages(): void {
  ToastrService.getInstance().clearOldMessages();
}

export function clearMessagesWithTag(tag: string): void {
  ToastrService.getInstance().clearMessagesWithTag(tag);
}

export function infoWithTimeout(message: string, timeout: number, tag: string): void {
  ToastrService.getInstance().infoWithTimeout(message, timeout, tag);
}


export default {
  error: error,
  info: info,
  warning: warning,
  success,
  clearOldMessages,
  clearMessagesWithTag,
  infoWithTimeout
}



