import {ApplicationComponentId, ApplicationId, OrganizationId, ScreenId} from "@libs/IdModel";
import {OrganizationNodeId, PersonId} from "@libs/OrganizationIdsModel";
import {I18nText} from "@libs/I18nText";
import {None, Option} from "@libs/Option";
import {AggregateId, AggregateVersion} from "@libs/AggregateId";


export class ScreenRef {
    private constructor(readonly id: string|undefined, // must be undefined instead of null for proper JSON serialization
                        readonly identifier: string|undefined) {}// must be undefined instead of null for proper JSON serialization

    static ofId(id: ScreenId) {
      return new ScreenRef(id.id, undefined);
    }

    static ofIdentifier(identifier: string) {
      return new ScreenRef(undefined, identifier);
    }

    static copy(other: ScreenRef) {
      if(other.id != undefined) {
        return new ScreenRef(other.id, undefined);
      } else {
        return new ScreenRef(undefined, other.identifier);
      }
    }

    idOption(): Option<ScreenId> {
      return Option.of(this.id).map(id => new ScreenId(id));
    }
    identifierOption(): Option<string> {
      return Option.of(this.identifier);
    }
  }

  export class ApplicationComponentRef {
    private constructor(readonly id: ApplicationComponentId|undefined, // must be undefined instead of null for proper JSON serialization
                readonly identifier: string|undefined) {}// must be undefined instead of null for proper JSON serialization

    static ofId(id: ApplicationComponentId) {
      return new ApplicationComponentRef(id, undefined);
    }

    static ofIdentifier(identifier: string) {
      return new ApplicationComponentRef(undefined, identifier);
    }

    static copy(other: ApplicationComponentRef) {
      if(other.id != undefined) {
        return new ApplicationComponentRef(ApplicationComponentId.copy(other.id), undefined);
      } else {
        return new ApplicationComponentRef(undefined, other.identifier);
      }
    }

    idOption(): Option<ApplicationComponentId> {
      return Option.of(this.id);
    }
    identifierOption(): Option<string> {
      return Option.of(this.identifier);
    }

    static ofScreenId(screenId: ScreenId) {
      return new ApplicationComponentRef(ApplicationComponentId.ofScreenId(screenId), undefined);
    }
  }



  export class ApplicationAuthorizations {
    constructor(readonly read: Array<OrganizationNodeId>,
                readonly edit: Array<OrganizationNodeId>,
                readonly groupsManagement: Array<OrganizationNodeId>,
                readonly access: Array<OrganizationNodeId>) {}

    static copy(other: ApplicationAuthorizations) {
      return new ApplicationAuthorizations(
        other.read.map(OrganizationNodeId.copy),
        other.edit.map(OrganizationNodeId.copy),
        other.groupsManagement.map(OrganizationNodeId.copy),
        other.access.map(OrganizationNodeId.copy)
      )
    }
  }

  export class ApplicationInfo {
    constructor(
      readonly id: ApplicationId,
      readonly version: AggregateVersion,
      readonly organizationId: OrganizationId,
      readonly name: I18nText,
      readonly codeName: string,
      readonly description: I18nText,
      readonly applicationCategoryId: number,
      readonly ownerId: PersonId,
      readonly authorizations: ApplicationAuthorizations
    ) {}

    static copy(other: ApplicationInfo) {
      return new ApplicationInfo(ApplicationId.copy(other.id),
        AggregateVersion.copy(other.version),
        OrganizationId.copy(other.organizationId),
        I18nText.copy(other.name),
        other.codeName,
        I18nText.copy(other.description),
        other.applicationCategoryId,
        PersonId.copy(other.ownerId),
        ApplicationAuthorizations.copy(other.authorizations));
    }
  }


  export class ApplicationName {
    constructor(readonly id: ApplicationId,
                readonly name: string,
                readonly identifier: string) {}


    static copy(other: ApplicationName) {
      return new ApplicationName(
        ApplicationId.copy(other.id),
        other.name, other.identifier);
    }
  }

  export class ApplicationSummary {
    constructor(readonly id: ApplicationId,
                public version: AggregateVersion,
                public name: string,
                public description: string,
                public categoryId: number,
                public ownerId: PersonId,
                readonly identifier: string) {}

    static copy(other: ApplicationSummary) {
      return new ApplicationSummary(
        ApplicationId.copy(other.id),
        AggregateVersion.copy(other.version),
        other.name,
        other.description,
        other.categoryId,
        PersonId.copy(other.ownerId),
        other.identifier);
    }
  }

  export class ApplicationsSearchQuery {

    constructor(public textQuery: Option<string>,
                public onlyOwnedByPerson: Option<AggregateId>) {}

    static empty(): ApplicationsSearchQuery {
      return new ApplicationsSearchQuery(None(), None());
    }
  }


