import {ApplicationId, DepartmentId, GroupId, ScreenId} from "./IdModel";
import {Typed} from "./Typed";
import {AggregateId} from "./AggregateId";

export interface AnyPersonId {
    className(): string;
    serialize(): string;
    asPersonId(): PersonId;
    asRemotePersonId(): RemotePersonId;
    equals(person: AnyPersonId): boolean;
    isPersonId(): boolean;
    isRemotePersonId(): boolean;
  }

  export class AnyPersonHelper {

    static equals(a: AnyPersonId, b: AnyPersonId): boolean {
      if(a instanceof PersonId && b instanceof PersonId) {
        return a.id.id === b.id.id;
      } else if(a instanceof RemotePersonId && b instanceof RemotePersonId) {
        return a.id === b.id && a.remoteOrganization === b.remoteOrganization;
      } else {
        return false;
      }
    }
  }

  export class AnyPersonIdFactory {
    static copyTyped(id: Typed<AnyPersonId>): Typed<AnyPersonId> {
      switch(Typed.className(id)) {
        case PersonId.className: return Typed.of(PersonId.copy(<PersonId>Typed.value(id)));
        case RemotePersonId.className: return Typed.of(RemotePersonId.copy(<RemotePersonId>Typed.value(id)));
        default: throw new Error("Unsupported type ["+Typed.className(id)+"]");
      }
    }

    static copy(id: AnyPersonId): AnyPersonId {
      switch(id.className()) {
        case PersonId.className: return PersonId.copy(<PersonId>id);
        case RemotePersonId.className: return RemotePersonId.copy(<RemotePersonId>id);
        default: throw new Error("Unsupported type ["+id.className()+"]");
      }

    }
  }


  export class RemotePersonId implements AnyPersonId {
    static className = "RemotePersonId";

    className(): string {
      return RemotePersonId.className;
    }

    constructor(readonly remoteOrganization: string,
                readonly id: string) {}

    static copy(other: RemotePersonId): RemotePersonId {
      return new RemotePersonId(other.remoteOrganization, other.id);
    }

    asPersonId(): PersonId {
      throw new Error("Not a local person");
    }

    asRemotePersonId(): RemotePersonId {
      return this;
    }

    equals(person: AnyPersonId): boolean {
      return AnyPersonHelper.equals(this, person);
    }

    serialize(): string {
      return this.remoteOrganization+"_"+this.id;
    }

    isPersonId(): boolean {
      return false;
    }

    isRemotePersonId(): boolean {
      return true;
    }
  }

  export class PersonId implements AnyPersonId {
        static className = "PersonId";

    private readonly _PersonId: undefined; // force TypeScript to check types (workaround for duck typing)

    className(): string {
      return PersonId.className;
    }

    constructor(public id: AggregateId){}

    static unique(ids: PersonId[]): PersonId[] {
      const uIds: PersonId[] = [];

      ids.forEach(id => {
        if(uIds.filter(uId => uId.id.id === id.id.id).length === 0) {
          uIds.push(id);
        }
      });

      return uIds;
    }

    static ANONYMOUS = new PersonId(new AggregateId("up8odosmhrys"))

    static copy(other: PersonId): PersonId {
      return new PersonId(AggregateId.copy(other.id));
    }

    static webClient(): PersonId {
      return new PersonId(new AggregateId("WebClient"));
    }

    static isWebClient(personId: PersonId): boolean {
      return AnyPersonHelper.equals(personId, PersonId.webClient())
    }

    serialize(): string {
      return this.id.id;
    }

    asPersonId(): PersonId {
      return this;
    }

    asRemotePersonId(): RemotePersonId {
      throw new Error("Not a remote person");
    }

    equals(person: AnyPersonId): boolean {
      return AnyPersonHelper.equals(this, person);
    }

    isPersonId(): boolean {
      return true;
    }

    isRemotePersonId(): boolean {
      return false;
    }

  }







  export class OrganizationNodeType {
    constructor(readonly name: string) {}

    static copy(other: OrganizationNodeType): OrganizationNodeType {
      switch (other.name) {
        case OrganizationNodeType.person.name: return OrganizationNodeType.person;
        case OrganizationNodeType.department.name: return OrganizationNodeType.department;
        case OrganizationNodeType.group.name: return OrganizationNodeType.group;
        case OrganizationNodeType.remoteOrganization.name: return OrganizationNodeType.remoteOrganization;
        case OrganizationNodeType.process.name: return OrganizationNodeType.process;
        case OrganizationNodeType.report.name: return OrganizationNodeType.report;
        case OrganizationNodeType.application.name: return OrganizationNodeType.application;
        case OrganizationNodeType.flow.name: return OrganizationNodeType.flow;
        case OrganizationNodeType.screen.name: return OrganizationNodeType.screen;
        case OrganizationNodeType.functions.name: return OrganizationNodeType.functions;
        default: throw new Error("Unsupported node type ["+other.name+"]");
      }
    }

    static person = new OrganizationNodeType("person");
    static department = new OrganizationNodeType("department");
    static group = new OrganizationNodeType("group");
    static remoteOrganization = new OrganizationNodeType("remoteOrganization");
    static process = new OrganizationNodeType("process");
    static report = new OrganizationNodeType("report");
    static application = new OrganizationNodeType("application");
    static flow = new OrganizationNodeType("flow");
    static screen = new OrganizationNodeType("screen");
    static functions = new OrganizationNodeType("functions");

  }



  export class OrganizationNodeId {

    private personId: PersonId|undefined = undefined; // ? Where is it used ?

    constructor(readonly id: AggregateId, readonly nodeType: OrganizationNodeType) {}

    static copy(other: OrganizationNodeId) {
      return new OrganizationNodeId(other.id, OrganizationNodeType.copy(other.nodeType))
    }

    // static fromNode(node: OrganizationNode) {
    //   if (node.isDepartment() || node.isOrganization()) {
    //     return this.fromDepartmentId(node.id);
    //   } else if (node.isPerson()) {
    //     return this.fromPersonId(node.id);
    //   } else if (node.isGroup()) {
    //     return this.fromGroupId(node.id);
    //   } else {
    //     throw new Error("Undefined parent node type detected");
    //   }
    // }

    static equals(a: OrganizationNodeId, b: OrganizationNodeId): boolean {
      return a.id.id === b.id.id && a.nodeType.name === b.nodeType.name;
    }

    static fromDepartmentId(departmentId: AggregateId) {
      return new OrganizationNodeId(departmentId, OrganizationNodeType.department);
    }

    static fromPersonId(personId: AggregateId) {
      return new OrganizationNodeId(AggregateId.copy(personId), OrganizationNodeType.person);
    }

    static fromGroupId(groupId: AggregateId) {
      return new OrganizationNodeId(groupId, OrganizationNodeType.group);
    }

    static fromApplicationId(applicationId: ApplicationId) {
      return new OrganizationNodeId(new AggregateId(applicationId.id), OrganizationNodeType.application);
    }

    static fromScreenId(screenId: ScreenId) {
      return new OrganizationNodeId(new AggregateId(screenId.id), OrganizationNodeType.application);
    }

    isPerson() {
      return this.nodeType.name === OrganizationNodeType.person.name;
    }

    isDepartment() {
      return this.nodeType.name === OrganizationNodeType.department.name;
    }

    isGroup() {
      return this.nodeType.name === OrganizationNodeType.group.name;
    }

    isRemoteOrganization() {
      return this.nodeType.name === OrganizationNodeType.remoteOrganization.name;
    }

    isProcess() {
      return this.nodeType.name === OrganizationNodeType.process.name;
    }

    isReport() {
      return this.nodeType.name === OrganizationNodeType.report.name;
    }

    isApplication() {
      return this.nodeType.name === OrganizationNodeType.application.name;
    }

    isScreen() {
      return this.nodeType.name === OrganizationNodeType.screen.name;
    }

    isFlow() {
      return this.nodeType.name === OrganizationNodeType.flow.name;
    }

    asPerson() {
      if(this.personId == undefined) {
        if(this.nodeType.name === OrganizationNodeType.person.name) {
          this.personId =  new PersonId(this.id);
        } else {
          throw new Error("Not a person");
        }
      }
      return this.personId;
    }


    asGroup() {
      if(this.nodeType.name === OrganizationNodeType.group.name) {
        return new GroupId(this.id);
      } else {
        throw new Error("Not a group");
      }
    }

    asDepartment() {
      if(this.nodeType.name === OrganizationNodeType.department.name) {
        return new DepartmentId(this.id);
      } else {
        throw new Error("Not a person");
      }
    }

    equals(other: OrganizationNodeId) {
      return this.id.id === other.id.id && this.nodeType.name === other.nodeType.name;
    }
  }
