import {ToastrEventBus} from "./ToastrEventBus";
import {Option, Some} from "../../Option";

export class ToastrCategory {
  static Global = "Global";
  static Task = "Task";
  static Case = "Case";
  static Application = "Application";
  static Process = "Process";
  static Report = "Report";
  static Datastore = "Datastore";
  static Constants = "Constants";
  static MapComments = "MapComments";
}

export class ToastrService {

  private static instance: ToastrService|undefined;

  private messages: Array<string> = [];
    eventBus = new ToastrEventBus();

  private constructor() {
  }

  static getInstance() {
    if (ToastrService.instance === undefined) {
      ToastrService.instance = new ToastrService();
    }
    return ToastrService.instance;
  }

  info(message: string, tag: string): void {
    this.eventBus.infoAdded(message, tag, Option.NoneSingleton);
  }

  warning(message: string, tag: string): void {
    this.eventBus.warningAdded(message, tag, Option.NoneSingleton);
  }

  error(message: string, tag: string): void {
    this.eventBus.errorAdded(message, tag, Option.NoneSingleton);
  }

  success(message: string, tag: string): void {
    this.eventBus.successAdded(message, tag, Option.NoneSingleton);
  }

  clearOldMessages(): void {
    this.eventBus.clearOldMessagesSent();
  }

  clearMessagesWithTag(tag: string): void {
    this.eventBus.clearMessagesWithTagSent(tag);
  }

  infoWithTimeout(message: string, timeout: number, tag: string) {
    this.eventBus.infoAdded(message, tag, Some(timeout));
  }
}
