  export function compareNumbers(a: number, b: number): number {

    if(a > b) {
      return 1;
    } else if(a === b) {
      return 0;
    } else {
      return -1;
    }
  }

  export function formatTruncated(a: number): string {
    const intPart = trunc(a);
    const numberOfIntDigits = Math.abs(intPart).toString().length;
    const maximumSignificantDigits = Math.max(numberOfIntDigits + 2, 3);
    return a.toLocaleString([],{maximumSignificantDigits: maximumSignificantDigits})
  }

  function trunc(v: number): number {
    v = +v;
    return (v - v % 1)   ||   (!isFinite(v) || v === 0 ? v : v < 0 ? -0 : 0);
  }
