import {Component, Input, OnInit, ViewContainerRef} from '@angular/core';
import {ScreenInstanceId} from "@libs/IdModel";
import {
  ScreenViewModel,
  ScreenExternalEventBus,
  ScreenInstanceService,
  ScreenQueryService,
  FilesService
} from "@screens";
import {ServerEventsService} from "../events/ServerEventsService";

@Component({
  selector: 'my-screen',
  templateUrl: './screen.component.html',
  styleUrls: ['./screen.component.scss']
})
export class ScreenComponent implements OnInit {

  _screenInstanceId: ScreenInstanceId|undefined;
  viewModel!: ScreenViewModel;
  @Input() terminateOnDestroy: boolean = false;
  @Input() externalEventBus: ScreenExternalEventBus|undefined;
  allowDebug: boolean = false;
  onNotFound: () => void|null = () => {};


  private initiated = false;

  constructor(private readonly screenInstanceService: ScreenInstanceService,
              private readonly serverEventsService: ServerEventsService,
              private readonly viewContainerRef: ViewContainerRef,
              private readonly screenQueryService: ScreenQueryService,
              private readonly filesService: FilesService) {
  }

  @Input() set screenInstanceId(screenInstanceId: ScreenInstanceId|undefined) {
    this._screenInstanceId = screenInstanceId;
    if(this.initiated) {
      this.init();
    }
  }

  ngOnInit(): void {
    this.init();
  }

  private init() {

    this.initiated = true;

    if(this._screenInstanceId === undefined) {
      throw new Error("No screenInstanceId");
    } else {


      this.screenQueryService.loadSkins((skins) => {

        if(this._screenInstanceId === undefined) {
          throw new Error("No screenInstanceId");
        } else {
          // const externalEventBus: ScreenExternalEventBus = this.externalEventBus !== null ? this.externalEventBus : new ScreenExternalEventBus();
          const externalEventBus: ScreenExternalEventBus = this.externalEventBus === undefined ? new ScreenExternalEventBus() : this.externalEventBus;

          this.viewModel = new ScreenViewModel(this.viewContainerRef.element.nativeElement, this._screenInstanceId, this.terminateOnDestroy,
            externalEventBus,
            this.screenInstanceService, this.serverEventsService, this.filesService,
            this.allowDebug, () => {
              if (this.onNotFound !== null) {
                this.onNotFound();
              }
            }, skins);
        }
      });
    }
  }

}
