import {__} from "./FunctionalExtensions";

export function onFormFocusLost(selections: Array<HTMLElement|string>, callback: (eventTarget?: EventTarget) => void): () => void { //returns cancel function

  const blurTimeout:number|null = null;

  const mouseDownListener = function (e: MouseEvent) {
    const target = e.target || e.srcElement;
    if(target !== null) {
      if (isNotInSelections(selections, <Node>target)) {
        callback(target);
        document.removeEventListener("mousedown", mouseDownListener, true);
        document.removeEventListener("focus", focusListener, true);
        // document.removeEventListener("blur", blurListener, true);
      }
      if (blurTimeout != null) {
        clearTimeout(blurTimeout);
      }
    }
  };


  // **** MP - for now we try to listen only on mouse events, this might be sufficient.
  // **** Old code remains here in case we need to handle keyboard based focus loss
  //

  const focusListener = function (e: FocusEvent) {
    const target = e.target || e.srcElement;
    if(target !== null) {
      if (isNotInSelections(selections, <Node>target)) {
        callback();
        document.removeEventListener("mousedown", mouseDownListener, true);
        document.removeEventListener("focus", focusListener, true);
        // document.removeEventListener("blur", blurListener, true);
      }
      if (blurTimeout != null) {
        clearTimeout(blurTimeout);
      }
    }
  };
  //
  //
  //const blurListener = function () {
  //  const target = event.target;
  //  if(blurTimeout!=null) {
  //    clearTimeout(blurTimeout);
  //  }
  //  if(isNotInSelections(selections,<Node>target)) {
  //    callback();
  //    console.log("blur 1");
  //    document.removeEventListener("onmousedown", clickListener, true);
  //    document.removeEventListener("focus", focusListener, true);
  //    document.removeEventListener("blur", blurListener, true);
  //  } else {
  //    blurTimeout = setTimeout(function() {
  //      callback();
  //      console.log("blur 2");
  //      document.removeEventListener("onmousedown", clickListener, true);
  //      document.removeEventListener("focus", focusListener, true);
  //      document.removeEventListener("blur", blurListener, true);
  //    }, 20);
  //  }
  //};

  setTimeout(function() {
    document.addEventListener("mousedown", mouseDownListener, true);
    document.addEventListener("focus", focusListener, true);
    //document.addEventListener("blur", blurListener, true);
  }, 20);

  // Call this to cancel listening function
  return function() {
    if(blurTimeout!==null) {
      clearTimeout(blurTimeout);
    }
    document.removeEventListener("mousedown", mouseDownListener, true);
    document.removeEventListener("focus", focusListener, true);
    //document.removeEventListener("blur", blurListener, true);
  }

}
//
export function isNotInSelections(selections: Array<HTMLElement|string>, node: Node) {
  return !__(selections).exists((selection: HTMLElement|string) => {
    if(typeof selection  == "string") {
      const containers = document.querySelectorAll(selection);
      for(let i = 0; i < containers.length; i++) {
        if(isIn(node, containers.item(i))) {
          return true;
        }
      }
      return false;
    } else {
      return isIn(node, selection);
    }
  })
}
//
export function isIn(element: Node, container: Node): boolean {
  let parent: Node|null = element;
  while(parent !== null) {
    if(parent === container) {
      return true;
    }
    parent = parent.parentNode;
  }
  return false;
}
