  export class NodeVariable implements BusinessVariable {
    static className = "NodeVariable";

    className() {
      return NodeVariable.className
    }

    toSearchable(): string {
      return "";
    }

    simpleValueType() {
      return 'Node';
    }

    value: string; //ReleaseId encrypted pipe NodeId

    getReleaseId() {
      const hash = this.value.indexOf("#");
      const pipe = this.value.indexOf("|");
      return new AggregateId(this.value.substring(hash + 1, pipe));
    }

    getNodeId() {
      const pipe = this.value.indexOf("|");
      return parseInt(this.value.substring(pipe + 1));
    }

    isEmpty(): boolean {
      return this.valueToSimpleString().length === 0;
    }

    valueToString(): string {
      return "Node#"+this.value;
    }

    valueToSimpleString(): string {
      return this.valueToString();
    }

    valueToSortableString(): string {
      const id = this.getNodeId();
      if(id < 10) {
        return "000" + id;
      } else if(id < 100) {
        return "00" + id;
      } else if(id < 1000) {
        return "0" + id
      } else {
        return "" + id;
      }
    }


    toJsonValue(): any {
      return this.valueToSimpleString();
    }

    constructor(value: string) {
      this.value = value;
    }

    isEqual(other: BusinessVariable|undefined): boolean {
      if(this === other) {
        return true;
      } else if(other == undefined || other.className() !== this.className()) {
        return false;
      } else {
        return (<NodeVariable>other).value === this.value;
      }
    }

    compare(other: BusinessVariable): number {
      if(other == undefined) {
        return 1;
      } else if (other instanceof NodeVariable) {
        return compareNumbers(this.getNodeId(), other.getNodeId());
      } else {
        return this.valueToSortableString().localeCompare(other.valueToSortableString(), undefined, {numeric: true, sensitivity: 'base'});
      }
    }

    copy() {
      return new NodeVariable(this.value);
    }

    toStringVariable(): StringVariable {
      return new StringVariable(this.valueToString());
    }

    bytesSize(): number {
      return 4 + this.value.length;
    }

  }


  export class FlowStatus {
    constructor(readonly name: string) {}

    translate(): string {
      return i18n("enum_case_status_" + this.name);
    }
    static created = new FlowStatus("created");
    static inProgress = new FlowStatus("inProgress");
    static delayed = new FlowStatus("delayed");
    static terminated = new FlowStatus("terminated");
    static completed = new FlowStatus("completed");

    static order = [FlowStatus.created, FlowStatus.inProgress, FlowStatus.terminated, FlowStatus.completed, FlowStatus.delayed];
    static usable = [FlowStatus.inProgress, FlowStatus.terminated, FlowStatus.completed];

    static compare(a: FlowStatus, b: FlowStatus): number {
      return __(FlowStatus.order).findIndexOf(s => s.name == a.name).getOrElse(0) - __(FlowStatus.order).findIndexOf(s => s.name == b.name).getOrElse(0);
    }

    static copy(other: FlowStatus) {
      return new FlowStatus(other.name);
    }

    static byName(name: string):FlowStatus {
      switch(name) {
        case FlowStatus.created.name: return FlowStatus.created;
        case FlowStatus.inProgress.name: return FlowStatus.inProgress;
        case FlowStatus.delayed.name: return FlowStatus.delayed;
        case FlowStatus.terminated.name: return FlowStatus.terminated;
        case FlowStatus.completed.name: return FlowStatus.completed;
        default: throw new Error("Not supported flow status ["+name+"]")
      }
    }
  }

