export class ArrayVariableType<T extends BusinessVariableType> implements BusinessVariableType {
  static className = "ArrayVariableType";
  className() {return ArrayVariableType.className}

  subType: Typed<T>;

  constructor(subType: Typed<T>) {
    this.subType = subType;
  }

  static of<T extends BusinessVariableType>(subType: T): ArrayVariableType<T> {
    return new ArrayVariableType(Typed.of(subType));
  }

  defaultValue() {
    return new ArrayVariable([]);
  }

  unwrappedSubtype(): T {
    return Typed.value<T>(this.subType);
  }

  createVariable(value: any) {
    return new ArrayVariable(value);
  }

  static typeName = "Array";
  typeName() {
    return ArrayVariableType.typeName + "[" + Typed.value(this.subType).typeName()+"]";
  }

  isEqual(other: BusinessVariableType): boolean {
    return this.typeEqual(other);
  }

  typeEqual(other: BusinessVariableType): boolean {
    return other.className() === ArrayVariableType.className && (<ArrayVariableType<BusinessVariableType>>other).unwrappedSubtype().typeEqual(this.unwrappedSubtype());
  }

  simpleTypeEqual(other: BusinessVariableType): boolean {
    return other.className() === ArrayVariableType.className && (<ArrayVariableType<BusinessVariableType>>other).unwrappedSubtype().typeEqual(this.unwrappedSubtype());
  }

  addFieldByPath(path: VariableTypePath, fieldType: Typed<BusinessVariableType>): number {
    if (this.unwrappedSubtype().className() === ArrayVariableType.className) {
      return (<ArrayVariableType<BusinessVariableType>><any>this.unwrappedSubtype()).addFieldByPath(path, fieldType);
    } else if (this.unwrappedSubtype().className() === ObjectVariableType.className) {
      return (<ObjectVariableType><any>this.unwrappedSubtype()).addFieldByPath(path, fieldType);
    } else {
      return -1;
    }
  }
}

export class ObjectVariableType implements BusinessVariableType {
  static className = "ObjectVariableType";
  className() {return ObjectVariableType.className}

  subTypes: Array<[string, Typed<BusinessVariableType>]>;

  constructor(subTypes: [string, Typed<BusinessVariableType>][]) {
    this.subTypes = subTypes;
  }

  static copy(other: ObjectVariableType): ObjectVariableType {
    return new ObjectVariableType(other.subTypes.map((s: [string, Typed<BusinessVariableType>]) => <[string, Typed<BusinessVariableType>]>[s[0], BusinessVariableTypeFactory.copyTyped(s[1])]))
  }

  defaultValue() {
    return new ObjectVariable({});
  }

  fieldTypeByName(fieldName: string): Option<BusinessVariableType> {
    return Option.of(this.subTypes.filter(s => s[0] === fieldName)[0]).map(r => Typed.value(r[1]));
  }

  fieldTypeByPath(path: VariableTypePath): Option<BusinessVariableType> {
    const variableAtHead = __(this.subTypes).find(field => field[0] === path.head());
    if (path.isRoot()) {
      return variableAtHead.map(tuple => Typed.value(tuple[1]));
    } else {
      return variableAtHead.flatMap(tuple => {
        if (Typed.className(tuple[1]) === ObjectVariableType.className) {
          return (<ObjectVariableType>Typed.value(tuple[1])).fieldTypeByPath(path.tail());
        } else {
          return None()
        }
      })
    }
  }

  createVariable(value: any) {
    return new ObjectVariable(value);
  }

  static typeName = "Object";
  typeName() {
    return ObjectVariableType.typeName;
  }

  isEqual(other: BusinessVariableType): boolean {
    return this.typeEqual(other);
  }

  typeEqual(other: BusinessVariableType): boolean {
    return other.className() === ObjectVariableType.className && this.fieldsTypesEqual(<ObjectVariableType>other);
  }

  simpleTypeEqual(other: BusinessVariableType): boolean {
    return other.className() === ObjectVariableType.className;
  }

  addField(fieldName: string, fieldType: Typed<BusinessVariableType>): number {
    const newField: [string, Typed<BusinessVariableType>] = [fieldName,fieldType];
    this.subTypes = this.subTypes.filter(type => type[0] !== fieldName);
    this.subTypes.push(newField);
    return this.subTypes.length - 1;
  }

  addFieldByPath(path: VariableTypePath, fieldType: Typed<BusinessVariableType>): number {
    if (path.isRoot()) {
      return this.addField(path.head(), fieldType)
    } else if (!path.isEmpty()) {
      return __(this.subTypes).find(entry => entry[0] === path.head()).map(entry => {
        if (Typed.className(entry[1]) === ObjectVariableType.className) {
          return (<ObjectVariableType>Typed.value(entry[1])).addFieldByPath(path.tail(), fieldType);
        } else if (Typed.className(entry[1]) === ArrayVariableType.className) {
          return (<ArrayVariableType<BusinessVariableType>>Typed.value(entry[1])).addFieldByPath(path.tail(), fieldType);
        } else {
          return -1;
        }
      }).getOrElse(-1);
    }
    return -1;
  }

  addFieldAtIndex(fieldName: string, fieldType: Typed<BusinessVariableType>, index: number) {
    const newField: [string, Typed<BusinessVariableType>] = [fieldName,fieldType];
    this.subTypes.splice(index, 0, newField);
  }

  addFieldByPathAtIndexAtPath(path: VariableTypePath, fieldType: Typed<BusinessVariableType>, index: number) {
    if (path.isRoot()) {
      return this.addFieldAtIndex(path.head(), fieldType, index)
    } else if (!path.isEmpty()) {
      __(this.subTypes).find(entry => entry[0] === path.head()).forEach(entry => {
        if (Typed.className(entry[1]) === ObjectVariableType.className) {
          (<ObjectVariableType>Typed.value(entry[1])).addFieldByPathAtIndexAtPath(path.tail(), fieldType, index)
        }
      });
    }
  }

  moveField(fromIndex: number, toIndex: number) {
    if(fromIndex < toIndex) {
      const startList = this.subTypes.filter((f, index) => index < fromIndex);
      const fromElem = this.subTypes.filter((f, index) => index === fromIndex);
      const movedList = this.subTypes.filter((f, index) => index > fromIndex && index < toIndex);
      const toElem = this.subTypes.filter((f, index) => index === toIndex);
      const endList = this.subTypes.filter((f, index) => index > toIndex);
      this.subTypes = startList.concat(toElem).concat(movedList).concat(fromElem).concat(endList);
    } else {
      const startList = this.subTypes.filter((f, index) => index < toIndex);
      const fromElem = this.subTypes.filter((f, index) => index === fromIndex);
      const movedList = this.subTypes.filter((f, index) => index > toIndex && index < fromIndex);
      const toElem = this.subTypes.filter((f, index) => index === toIndex);
      const endList = this.subTypes.filter((f, index) => index > fromIndex);
      this.subTypes = startList.concat(fromElem).concat(movedList).concat(toElem).concat(endList);
    }
  }

  removeField(fieldName: string) {
    const index = __(this.subTypes).findIndexOf(entry => entry[0] === fieldName);
    index.forEach(idx => this.subTypes.splice(idx, 1));
  }

  removeFieldAtIndex(index: number) {
    this.subTypes.splice(index, 1);
  }

  removeFieldByPath(path: VariableTypePath) {
    if (path.isRoot()) {
      this.removeField(path.head())
    } else if (!path.isEmpty()) {
      __(this.subTypes).find(entry => entry[0] === path.head()).forEach(entry => {
        if (Typed.className(entry[1]) === ObjectVariableType.className) {
          (<ObjectVariableType>Typed.value(entry[1])).removeFieldByPath(path.tail())
        }
      })
    }
  }

  removeFieldBydPathAtIndex(path: VariableTypePath, index: number) {
    if (path.isEmpty()) {
      this.removeFieldAtIndex(index)
    } else if (!path.isEmpty()) {
      __(this.subTypes).find(entry => entry[0] === path.head()).forEach(entry => {
        if (Typed.className(entry[1]) === ObjectVariableType.className) {
          (<ObjectVariableType>Typed.value(entry[1])).removeFieldBydPathAtIndex(path.tail(), index)
        }
      })
    }
  }

  renameField(oldName: string, newName: string) {
    __(this.subTypes).find(entry => entry[0] === oldName).forEach(entry => entry[0] = newName);
  }

  renameFieldByPath(path: VariableTypePath, newName: string) {
    if (path.isRoot()) {
      this.renameField(path.head(), newName)
    } else if (!path.isEmpty()) {
      __(this.subTypes).find(entry => entry[0] === path.head()).forEach(entry => {
        if (Typed.className(entry[1]) === ObjectVariableType.className) {
          (<ObjectVariableType>Typed.value(entry[1])).renameFieldByPath(path.tail(), newName)
        }
      });
    }
  }

  changeFieldType(name: string, newType: Typed<BusinessVariableType>) {
    __(this.subTypes).find(entry => entry[0] === name).forEach(entry => entry[1] = newType);
  }

  changeFieldTypeByPath(path: VariableTypePath, newType: Typed<BusinessVariableType>) {
    if (path.isRoot()) {
      this.changeFieldType(path.head(), newType)
    } else if (!path.isEmpty()) {
      __(this.subTypes).find(entry => entry[0] === path.head()).forEach(entry => {
        if (Typed.className(entry[1]) === ObjectVariableType.className) {
          (<ObjectVariableType>Typed.value(entry[1])).changeFieldTypeByPath(path.tail(), newType)
        }
      });
    }
  }

  findIndexByPath(path: VariableTypePath): Option<number> {
    if (path.isRoot()) {
      return __(this.subTypes).findIndexOf(field => field[0] === path.head())
    } else {
      return __(this.subTypes).find(entry => entry[0] === path.head()).flatMap(entry => {
        if (Typed.className(entry[1]) === ObjectVariableType.className) {
          return (<ObjectVariableType>Typed.value(entry[1])).findIndexByPath(path.tail())
        } else {
          return None();
        }
      });
    }
  }

  private fieldsTypesEqual(other: ObjectVariableType): boolean {
    const thisFields = this.subTypes.map(s => s[0]).sort();
    const otherFields = this.subTypes.map(s => s[0]).sort();

    // const sameFieldsNames = thisFields.length === otherFields.length && _.intersection(thisFields, otherFields).length === otherFields.length;

    // return sameFieldsNames && _.every(thisFields, (field: string) => this.fieldTypeByName(field).value.typeEqual(other.fieldTypeByName(field).value));
    return true;
  }
}

