export class BusinessVariableTypeFactory {
  static copy(variable: BusinessVariableType): BusinessVariableType {
    return BusinessVariableTypeFactory.copyByType(variable, variable.className());
  }

  static copyTyped(variable: Typed<BusinessVariableType>): Typed<BusinessVariableType> {
    return Typed.of(BusinessVariableTypeFactory.copyByType(Typed.value(variable), Typed.className(variable)));
  }

  static copyByType(variable: BusinessVariableType, className: string): BusinessVariableType {
    switch (className) {
      case StringVariableType.className: return new StringVariableType();
      case PasswordVariableType.className: return new PasswordVariableType();
      case I18nTextVariableType.className: return new I18nTextVariableType();
      case NumberVariableType.className: {
        const v: NumberVariableType = <NumberVariableType>variable;
        return new NumberVariableType(v.percent, v.precision, v.unit);
      }
      case BooleanVariableType.className: return new BooleanVariableType();
      case DateVariableType.className: return new DateVariableType();
      case DateTimeVariableType.className: return new DateTimeVariableType();
      case TimeVariableType.className: return new TimeVariableType();
      case IntervalVariableType.className: return new IntervalVariableType();
      case GeoCoordinateVariableType.className: return new GeoCoordinateVariableType();
      case FileVariableType.className: return new FileVariableType();
      case PersonVariableType.className: return new PersonVariableType();
      case DepartmentVariableType.className: return new DepartmentVariableType();
      case GroupVariableType.className: return new GroupVariableType();
      case OrganizationNodeVariableType.className: return new OrganizationNodeVariableType();
      case ArrayVariableType.className: {
        const v: ArrayVariableType<any> = <ArrayVariableType<any>>variable;
        return new ArrayVariableType<any>(BusinessVariableTypeFactory.copyTyped(v.subType));
      }
      case ObjectVariableType.className: {
        const v: ObjectVariableType = <ObjectVariableType>variable;
        return ObjectVariableType.copy(v);
      }
      case TaskVariableType.className: return new TaskVariableType();
      case CaseVariableType.className: return new CaseVariableType();
      case CaseCodeVariableType.className: return new CaseCodeVariableType();
      case CaseStatusVariableType.className: return new CaseStatusVariableType();
      case InstanceVariableType.className: return new InstanceVariableType();
      case ProcessVariableType.className: return new ProcessVariableType();
      case ProcessVersionVariableType.className: return new ProcessVersionVariableType();
      case NodeVariableType.className: return new NodeVariableType();
      case LinkVariableType.className: return new LinkVariableType();
      case EmailVariableType.className: return new EmailVariableType();
      default: throw new Error("Unsupported variable: " + JSON.stringify(variable));
    }
  }

  static createFromVariable(variable: BusinessVariable): BusinessVariableType {
    switch (variable.className()) {
      case StringVariable.className: {
        return new StringVariableType();
      }
      case PasswordVariable.className: {
        return new PasswordVariableType();
      }
      case NumberVariable.className: {
        return new NumberVariableType();
      }
      case BooleanVariable.className: return new BooleanVariableType();
      case DateVariable.className: {
        return new DateVariableType();
      }
      case DateTimeVariable.className: {
        return new DateTimeVariableType();
      }
      case TimeVariable.className: return new TimeVariableType();
      case IntervalVariable.className: return new IntervalVariableType();
      case GeoCoordinateVariable.className: return new GeoCoordinateVariableType();
      case FileVariableV2.className: {
        return new FileVariableType();
      }
      case PersonVariable.className: return new PersonVariableType();
      case DepartmentVariable.className: return new DepartmentVariableType();
      case GroupVariable.className: return new GroupVariableType();
      case ArrayVariable.className: {
        let variables = (<ArrayVariable<BusinessVariable>>variable).unwrappedValue();
        return ArrayVariableType.of(variables.length > 0 ? BusinessVariableTypeFactory.createFromVariable(variables[0]) : new StringVariableType());
      }
      case ObjectVariable.className: {
        throw new Error("Object not upported");// null // TODO
      }
      case TaskVariable.className: return new TaskVariableType();
      case CaseVariable.className: return new CaseVariableType();
      case CaseStatusVariable.className: return new CaseStatusVariableType();
      case InstanceVariable.className: return new InstanceVariableType();
      case ProcessVariable.className: return new ProcessVariableType();
      case ProcessVersionVariable.className: return new ProcessVersionVariableType();
      case NodeVariable.className: return new NodeVariableType();
      case LinkVariable.className: return new LinkVariableType();
      case EmailVariableType.className: return new EmailVariableType();
      default: throw new Error("Unsupported variable: " + JSON.stringify(variable));
    }
  }
}
