  export interface CreateScreenSagaResponse {
    className(): string;
  }

  export class ScreenWithReleasesCreated implements CreateScreenSagaResponse {
    static className = "ScreenWithReleasesCreated";
    className() {
      return ScreenWithReleasesCreated.className;
    }

    constructor(public screenId: ScreenId,
                public screenVersion: AggregateVersion) {}

    static copy(other: ScreenWithReleasesCreated) {
      return new ScreenWithReleasesCreated(ScreenId.copy(other.screenId), AggregateVersion.copy(other.screenVersion))
    }
  }

  export class ScreenWithReleasesCreationFailure implements CreateScreenSagaResponse {
    static className = "ScreenWithReleasesCreationFailure";
    className() {
      return ScreenWithReleasesCreationFailure.className;
    }

    constructor(public exceptions: string[]) {}

    static copy(other: ScreenWithReleasesCreationFailure) {
      return new ScreenWithReleasesCreationFailure(other.exceptions.slice());
    }
  }


