  export class CompilationResult {
    constructor(readonly result: Option<ScreenComponents>,
                readonly errors: Array<string>) {}

    get success() {
      return this.result.isDefined();
    }

    static success(result: ScreenComponents) {
      return new CompilationResult(Some(result), []);
    }

    static error(error: string) {
      return new CompilationResult(None(), [error]);
    }

    static errors(errors: Array<string>) {
      return new CompilationResult(None(), errors);
    }


  }

