export class ComponentClipboardActionProperties {
  constructor(readonly properties: Array<[string, ComponentClipboardActionProperty]>) {
  }

  putActions(propertyName: string, automaticActions: Array<AutomaticActionRef>, validationRulesRefs: Array<ValidationRuleRef>, validationRules: Array<ValidationRule>) {
    const index = __(this.properties).findIndexOf(e => e[0] == propertyName);
    index.forEach(i => this.properties.splice(i, 1));
    this.properties.push([propertyName, new ComponentClipboardActionProperty(automaticActions, validationRulesRefs, validationRules)]);
  }

  static copy(other: ComponentClipboardActionProperties) {
    return new ComponentClipboardActionProperties(other.properties.map(p => <[string, ComponentClipboardActionProperty]>[p[0], ComponentClipboardActionProperty.copy(p[1])]))
  }

  forEach<T>(handler: (propertyName: string, actionsRefs: Array<AutomaticActionRef>, validationRulesRefs: Array<ValidationRuleRef>) => T): Array<T> {
    return this.properties.map((p: [string, ComponentClipboardActionProperty]) => handler(p[0], p[1].actions, p[1].validationRulesRefs));
  }

  getActionRule(name: string, ruleId: ValidationRuleId): ValidationRule {
    const actionProperty = __(this.properties).find(p => p[0] == name).getOrError("no action '" + name + "' found")[1];
    return __(actionProperty.validationRules).find(r => r.id.id === ruleId.id).getOrError("no rule " + ruleId.id + " found");
  }
}

