import {HttpClientJsonpModule, HttpClientModule} from "@angular/common/http";

import {APP_INITIALIZER, NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';

import {ScreenComponent, ScreenComponentComponent} from './screen';
import {RouterTestingModule} from "@angular/router/testing";
import {AppComponent} from "./app.component";
import {ScreenPortletComponent} from "./screen/screen-portlet.component";
import {SectionContainerComponent} from "./screen/components/section/section-container.component";
import {LabelComponentComponent} from "./screen/components/label/label-component.component";
import {NumberInputComponentComponent} from "./screen/components/input/number-input-component.component";
import {TextInputComponentComponent} from "./screen/components/input/text-input-component.component";
import {PasswordInputComponentComponent} from "./screen/components/input/password-input-component.component";
import {FormsModule} from "@angular/forms";
import {ComponentLabelComponent} from "./screen/components/component-label.component";
import {ButtonComponentComponent} from "./screen/components/button/button-component.component";
import {SingleCheckboxComponentComponent} from "./screen/components/select/single-checkbox-component.component";
import {RepeatableContainerComponent} from "./screen/components/repeatable/repeatable-container.component";
import {TabsContainerComponent} from "@screens";
import {TableContainerComponent} from "@screens";
import {ImageComponentComponent} from "./screen/components/image/image-component.component";
import {MultiCheckboxComponentComponent} from "./screen/components/select/multi-checkbox-component.component";
import {RadioButtonComponentComponent} from "./screen/components/select/radio-button-component.component";
import {LinkComponentComponent} from "./screen/components/link/link-component.component";
import {DropListComponentComponent} from "./screen/components/select/drop-list-component.component";
import {DateInputComponentComponent} from "./screen/components/date/date-input-component.component";
import {DropMenuComponent} from "./screen/utils/drop-menu.component";
import {MapComponentComponent} from "./screen/components/map/map-component.component";
import {GoogleMapsModule} from "@angular/google-maps";
import {AdjustingTextInput} from "@libs/components/adjusting-text-input.component";
import {I18nService} from "./shared/internationalization/i18n.service";
import {I18nPipe} from "./shared/pipes/I18nPipe";
import {HtmlComponentComponent} from "./screen/components/html/html-component.component";
import {SwitchComponentComponent} from "./screen/components/switch/switch-component.component";
import {
  MultiAttachmentInputComponentComponent
} from "./screen/components/attachment/multi-attachment-input-component.component";
import {
  SingleAttachmentInputComponentComponent
} from "./screen/components/attachment/single-attachment-input-component.component";
import {FileSizeFormatPipe} from "./shared/pipes/FileSizeFormatPipe";
import {TimeSummaryComponent} from "@libs/components/time-summary.component";
import {ToastrOverlayComponent} from "@libs/components/toastr/toastr-overlay.component";
import {SanitizeStylePipe} from "./shared/pipes/sanitize-style.pipe";

@NgModule({
  imports: [
    BrowserModule,
    HttpClientModule,
    HttpClientJsonpModule,
    RouterTestingModule,
    FormsModule,
    GoogleMapsModule
  ],
  declarations: [
    AppComponent,
    ScreenPortletComponent,
    ToastrOverlayComponent,
    AdjustingTextInput,
    TimeSummaryComponent,
    ScreenComponent,
    DropMenuComponent,
    ScreenComponentComponent,
    SectionContainerComponent,
    RepeatableContainerComponent,
    TabsContainerComponent,
    TableContainerComponent,
    LabelComponentComponent,
    HtmlComponentComponent,
    SwitchComponentComponent,
    ButtonComponentComponent,
    LinkComponentComponent,
    MapComponentComponent,
    DropListComponentComponent,
    ImageComponentComponent,
    SingleCheckboxComponentComponent,
    MultiCheckboxComponentComponent,
    RadioButtonComponentComponent,
    NumberInputComponentComponent,
    TextInputComponentComponent,
    PasswordInputComponentComponent,
    MultiAttachmentInputComponentComponent,
    SingleAttachmentInputComponentComponent,
    ComponentLabelComponent,
    I18nPipe,
    FileSizeFormatPipe,
    SanitizeStylePipe,
    DateInputComponentComponent
  ],
  providers: [{
    provide: APP_INITIALIZER,
    useFactory: resourceProviderFactory,
    deps: [I18nService],
    multi: true
  }],
  bootstrap: [AppComponent]
})
export class AppModule { }

export function resourceProviderFactory(provider: I18nService) {
  return () => provider.initialize();
}
