/**
 * Function for clearing array in place.
 */
import {Typed} from "./Typed";


export function clearArray(array: any[]) {
  while(array.length > 0) {
    array.pop();
  }
}

export function overwriteArray(dst: any[], src: any[]): void {
  while(dst.length > 0) {
    dst.pop();
  }
  src.forEach(e => dst.push(e));
}


/** remove element from given array, element must be in array */
export function removeFromArray<T>(array: T[], element: T) {
  const index = array.indexOf(element);
  if (index > -1) {
    array.splice(index, 1);
  } else throw new Error("element not found at the array");
}

export function createArrayFrom0ToNExclusive(n: number) {
  const numbers: number[] = [];
  for(let i = 0; i< n; i++) {
    numbers.push(i);
  }
  return numbers;
}

export function createArray<T>(n: number, element: T) {
  const arr: T[] = [];
  for(let i = 0; i< n; i++) {
    arr.push(element);
  }
  return arr;
}

export function indexOfTyped<T extends {className():string}>(arr: Typed<T>[], element: Typed<T>): number {
  if(element) {
    for(let i = 0; i < arr.length; i++) {
      if(Typed.value(arr[i]) === Typed.value(element)) {
        return i;
      }
    }
  }
  return -1;
}

