// works for GridProcessNode, GridProcessEdge, GridProcessActor...
export function buildIdMap<T extends {id: number}>(elems: T[]): { [id: number]: T} {
  const result: { [id: number]: T} = {};
  elems.forEach(elem => {
    result[elem.id] = elem;
  });
  return result;
}


export function moveElementInArray<T>(array: T[], fromIndex: number, toIndex: number): T[] {
	if (toIndex >= array.length) {
		// var k = toIndex - array.length;
		// while ((k--) + 1) {
		// 	array.push(undefined);
		// }
    throw new Error("Not implemented");
	}
	array.splice(toIndex, 0, array.splice(fromIndex, 1)[0]);
	return array;
}


class Mutable<T> {
  value: T;
  constructor(value: T) {
    this.value = value;
  }
}



