import {Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewContainerRef} from "@angular/core";

@Component({
  selector: 'my-adjusting-text-input[contenteditable][ngDefaultControl]',
  template: '',
  styleUrls: ['./adjusting-text-input.component.scss']
})
export class AdjustingTextInput implements OnInit {

  @Output() blur: EventEmitter<void> = new EventEmitter<void>();

  @Input() set model(m: string) {
    this.innerValue = m;
    this.getNativeElement().innerText = m;
    this.showPlaceholder();
    console.log("Model changed", m);
  }

  @Output() modelChange = new EventEmitter<string>();

  @Input() set placeholder(p: string) {
    this._placeholder = p;
    this.showPlaceholder();
  }

  private innerValue: string = "";
  private placeholderVisible = false;
  private _placeholder: string = "";
  private focused = false;

  constructor(private readonly viewContainerRef: ViewContainerRef) {
    const element = this.getNativeElement();
    element.addEventListener("blur", this.onBlur);
    element.addEventListener('paste', this.onPaste);
    element.addEventListener("focus", this.onFocus);
  }

  ngOnInit(): void {
    this.showPlaceholder();
  }

  private showPlaceholder() {
    const element = this.getNativeElement();
    if(!this.focused && this._placeholder.trim().length > 0 && this.innerValue.length === 0) {
      this.placeholderVisible = true;
      element.innerText = this._placeholder;
      element.classList.add("placeholder")
    } else {
      this.placeholderVisible = false;
      element.innerText = this.innerValue;
      element.classList.remove("placeholder")
    }
  }


  private getNativeElement() {
    return <HTMLElement>this.viewContainerRef.element.nativeElement;
  }

  private onFocus = () => {
    this.focused = true;
    const element = this.getNativeElement();
    if(this.placeholderVisible) {
      element.innerHTML = "";
      element.classList.remove("placeholder");
      this.placeholderVisible = false;
    }
  }

  private onPaste = (event: ClipboardEvent) => {

    event.preventDefault();
    let paste = (event.clipboardData || (<any>window).clipboardData).getData('text/plain');

    const selection = window.getSelection();
    if(selection !== null) {
      if (!selection.rangeCount) return;
      selection.deleteFromDocument();
      let node = document.createTextNode(paste);
      selection.getRangeAt(0).insertNode(node);
      selection.setPosition(node, paste.length);
    }
  }

  private onBlur = () => {
    this.focused = false;
    if(!this.placeholderVisible) {
      this.innerValue = this.getNativeElement().innerText;
      if (this.innerValue !== null) {
        this.modelChange.emit(this.innerValue);
      } else {
        this.modelChange.emit("");
      }
      this.showPlaceholder();
      this.blur.emit();
    } else {
      throw new Error("Placeholder should not be visible");
    }
  };


}
