/**
 * Immutable wrapper for width and height values.
 */
export class Size {

  constructor(readonly width:number, readonly height:number) {}

  /**
   * It returns new Size object scaled by a given factor.
   */
  scale(factor: number) {
    return new Size(this.width * factor, this.height * factor);
  }

  plus(plusWidth: number, plusHeight: number) {
    return new Size(this.width + plusWidth, this.height + plusHeight);
  }
}


class Padding {

  private _top: number;
  private _right: number;
  private _bottom: number;
  private _left: number;


  constructor(top: number, right: number, bottom: number, left: number) {
    this._top = top;
    this._right = right;
    this._bottom = bottom;
    this._left = left;
  }


  get top(): number {
    return this._top;
  }

  get right(): number {
    return this._right;
  }

  get bottom(): number {
    return this._bottom;
  }

  get left(): number {
    return this._left;
  }

  toCss(unit: string) {
    return this._top + unit+" "+this._right + unit+" "+this._bottom + unit+" "+this._left + unit;
  }
}
