export function arraysEqual(arrayA: string[], arrayB: string[]) {
  if (arrayA.length !== arrayB.length) {
    return false;
  } else {
    for (let i = 0; i < arrayA.length; i++) {
      if (arrayA[i] !== arrayB[i]) {
        return false;
      }
    }
    return true;
  }
}

export function arraysEqualBy<T>(arrayA: T[], arrayB: T[], equals: (a: T, b: T) => boolean) {
  if (arrayA.length !== arrayB.length) {
    return false;
  } else {
    for (let i = 0; i < arrayA.length; i++) {
      if (!equals(arrayA[i], arrayB[i])) {
        return false;
      }
    }
    return true;
  }
}


export function range(length: number) {
  const r = [];
  for(let i = 0; i < length; i++) {
    r[i] = i;
  }
  return r;
}
