import {MultiTypeInput, MultiTypeInputFactory} from "./MultiTypeInputModel";
import {VariablePath} from "./VariablePath";
import {AutomaticActionId} from "./IdModel";
import {Option} from "./Option";
import {Typed} from "./Typed";
import {i18n} from "../shared/internationalization/i18n.service";

export class AutomaticActionType {

    constructor(readonly name: string) {}

    static readonly script = new AutomaticActionType("script");
    static readonly setValue = new AutomaticActionType("setValue");
    static readonly clearValue = new AutomaticActionType("clearValue");
    static readonly email = new AutomaticActionType("email");
    static readonly notification = new AutomaticActionType("notification");
    static readonly sms = new AutomaticActionType("sms");
    static readonly existingAction = new AutomaticActionType("existingAction");
  }





  export interface AutomaticAction {
    id: AutomaticActionId;
    className(): string;
    async: boolean;
    name: Option<string>;
    identifier: Option<string>;
    conditional: boolean;
    conditionExpression: string;
    saveTo: Array<any>; // should be VariablePath but inheritance has some problems
    searchableText(): string;
  }


  export class ScriptAction implements AutomaticAction {

    static className = "ScriptAction";
    className(): string {
      return ScriptAction.className;
    }

    constructor(readonly id: AutomaticActionId,
                readonly async: boolean,
                readonly name: Option<string>,
                readonly identifier: Option<string>,
                readonly conditional: boolean,
                readonly conditionExpression: string,
                readonly script: string,
                readonly saveTo: Array<VariablePath>) {}

    static copy(other: ScriptAction) {
      return new ScriptAction(
        AutomaticActionId.copy(other.id),
        other.async,
        Option.copy(other.name),
        Option.copy(other.identifier),
        other.conditional,
        other.conditionExpression,
        other.script,
        other.saveTo.map(VariablePath.copy)
      );
    }

    searchableText() {
      return this.name.getOrElse("")+" "+(this.conditional ? this.conditionExpression : "")+ " "+this.script+" "+this.saveTo.map(s => s.path).join(" ");
    }

  }


