  export class EmailAction implements AutomaticAction {

    static className = "EmailAction";
    className(): string {
      return EmailAction.className;
    }

    readonly defaultVisibleLabel = i18n("scriptActions_Email");

    constructor(readonly id: AutomaticActionId,
                readonly async: boolean,
                readonly name: Option<string>,
                readonly identifier: Option<string>,
                readonly conditional: boolean,
                readonly conditionExpression: string,
                readonly emailRecipients: Array<Typed<MultiTypeInput>>,
                readonly emailSubject: Typed<MultiTypeInput>,
                readonly emailBody: Typed<MultiTypeInput>,
                readonly emailAttachments: Typed<MultiTypeInput>,
                readonly disableResponse: boolean,
                readonly saveTo: Array<VariablePath>) {}

    static copy(other: EmailAction) {
      return new EmailAction(
        AutomaticActionId.copy(other.id),
        other.async,
        Option.copy(other.name),
        Option.copy(other.identifier),
        other.conditional,
        other.conditionExpression,
        other.emailRecipients.map(MultiTypeInputFactory.copyTyped),
        MultiTypeInputFactory.copyTyped(other.emailSubject),
        MultiTypeInputFactory.copyTyped(other.emailBody),
        MultiTypeInputFactory.copyTyped(other.emailAttachments),
        other.disableResponse,
        other.saveTo.map(VariablePath.copy)
      );
    }

    emailRecipientsUnwrapped(): Array<MultiTypeInput> {
      return this.emailRecipients.map(v => Typed.value(v));
    }
    emailSubjectUnwrapped() {
      return Typed.value(this.emailSubject);
    }
    emailBodyUnwrapped() {
      return Typed.value(this.emailBody);
    }
    emailAttachmentsUnwrapped() {
      return Typed.value(this.emailAttachments);
    }

    searchableText() {
      return this.name.getOrElse("")+" "+(this.conditional ? this.conditionExpression : "")+ " "+
        this.emailRecipientsUnwrapped().map(v => v.searchableText())+" "+this.emailSubjectUnwrapped().searchableText()+" "+
        this.emailBodyUnwrapped().searchableText() + this.emailAttachmentsUnwrapped().searchableText();
    }

  }


