  export class SMSAction implements AutomaticAction {
    static className = "SMSAction";
    className(): string {
      return SMSAction.className;
    }

    constructor(readonly id: AutomaticActionId,
                readonly async: boolean,
                readonly name: Option<string>,
                readonly identifier: Option<string>,
                readonly conditional: boolean,
                readonly conditionExpression: string,
                readonly phoneNumbers: Array<Typed<MultiTypeInput>>,
                readonly smsMessage: Typed<MultiTypeInput>,
                readonly test: boolean,
                readonly saveTo: Array<VariablePath>) {}


    static copy(other: SMSAction) {
      return new SMSAction(
        AutomaticActionId.copy(other.id),
        other.async,
        Option.copy(other.name),
        Option.copy(other.identifier),
        other.conditional,
        other.conditionExpression,
        other.phoneNumbers.map(MultiTypeInputFactory.copyTyped),
        MultiTypeInputFactory.copyTyped(other.smsMessage),
        other.test,
        other.saveTo.map(VariablePath.copy)
      );
    }


    phoneNumbersUnwrapped(): Array<MultiTypeInput> {
      return this.phoneNumbers.map(v => Typed.value(v));
    }
    smsMessageUnwrapped() {
      return Typed.value(this.smsMessage);
    }

    searchableText() {
      return this.name.getOrElse("")+" "+(this.conditional ? this.conditionExpression : "")+ " "+
        this.phoneNumbersUnwrapped().map(v => v.searchableText())+" "+this.smsMessageUnwrapped().searchableText();
    }
  }


  export class AutomaticActionFactory {
    static copy(input: AutomaticAction): AutomaticAction {
      return AutomaticActionFactory.copyByType(input, input.className());
    }

    static copyTyped(action: Typed<AutomaticAction>): Typed<AutomaticAction> {
      return Typed.of(AutomaticActionFactory.copyByType(Typed.value(action), Typed.className(action)));
    }

    static copyByType(action: AutomaticAction, className: string): AutomaticAction {
      switch (className) {
        case ScriptAction.className: return ScriptAction.copy(<ScriptAction>action);
        case SetValueAction.className: return SetValueAction.copy(<SetValueAction>action);
        case ClearValueAction.className: return ClearValueAction.copy(<ClearValueAction>action);
        case EmailAction.className: return EmailAction.copy(<EmailAction>action);
        case NotificationAction.className: return NotificationAction.copy(<NotificationAction>action);
        case SMSAction.className: return SMSAction.copy(<SMSAction>action);

        default:
          throw new Error("Unsupported action class: " + className + ", action: " + JSON.stringify(action));
      }
    }
  }


