import {Component, ElementRef, Input, OnDestroy, OnInit, ViewChild, ViewContainerRef} from "@angular/core";
import {DropListComponentViewModel, SelectComponentOption} from "@screens";
import {onFormFocusLost} from "@libs/Dom";

@Component({
  selector: 'my-drop-list-component[viewModel]',
  templateUrl: './drop-list-component.component.html',
  // styleUrls: ['./drop-list-component.component.scss']
})
export class DropListComponentComponent implements OnDestroy {
  @Input() viewModel!: DropListComponentViewModel;

  @ViewChild("DropListComponent") container: ElementRef<HTMLElement>|undefined;
  @ViewChild("dropListPopup") dropListPopup: ElementRef<HTMLElement>|undefined;

  enabled = true;
  open = false;
  focused = false;

  private keyboardListeningEnabled = false;

  constructor(private readonly viewContainerRef: ViewContainerRef) {
  }

  private isEnabled(){
    return this.viewModel.editable;
  }

  onClick = () => {
    if(this.isEnabled()) {
      if(this.open) {
        this.closePicker();
      } else {
        this.openPicker();
      }
    }
  }

  openPicker = () => {
    this.open = true;
  }

  onDropMenuOpened(popup: HTMLElement) {
    setTimeout(() => {
      const dropListFilter = <HTMLInputElement>popup.querySelector(".dropListFilter");
      if(dropListFilter !== null) { // it's optional
        dropListFilter.select();
      }
    });
  }

  private closePicker = () => {
    this.open = false;
  }

  optionSelected = (option: SelectComponentOption) => {
    this.closePicker();
    this.viewModel.optionSelected(option);
  }


  onFocus = () => {

    if(!this.keyboardListeningEnabled) {
      this.enableKeyEvents();
    }

    if(!this.focused && this.isEnabled()){
      this.focused = true;

      if(this.container === undefined) {
        throw new Error("No container");
      } else {
        this.container.nativeElement.classList.add("focused");
        onFormFocusLost([this.viewContainerRef.element.nativeElement], this.onBlur);

      }
    }
  }

   onBlur = () => {
     if(this.focused){
       if(this.container === undefined) {
         throw new Error("No container");
       } else {
         this.focused = false;
         this.container.nativeElement.classList.remove("focused");
       }
     }
   }

  onKeyEvent = (event: KeyboardEvent) => {
    if (this.isEnabled() && this.focused) {
      if (this.open) {
        switch (event.key) {
          case 'ArrowUp' : {
            this.viewModel.activePrevious();
            event.preventDefault();
            break;
          }
          case 'ArrowDown' : {
            this.viewModel.activeNext();
            event.preventDefault();
            break;
          }
          case 'Enter' : {
            this.viewModel.selectActive();
            setTimeout(this.closePicker);
            event.preventDefault();
            break;
          }
          case 'Escape' : {
            this.closePicker();
            event.preventDefault();
            break;
          }
        }
      } else {
        switch(event.key){
          case 'Enter' : {
            this.onClick();
            event.preventDefault();
            break;
          }
          case 'ArrowUp' : {
            this.viewModel.activePrevious();
            this.viewModel.selectActive();
            this.viewModel.updateActiveIndexToSelected();
            event.preventDefault();
            break;
          }
          case 'ArrowDown' : {
            this.viewModel.activeNext();
            this.viewModel.selectActive();
            this.viewModel.updateActiveIndexToSelected();
            event.preventDefault();
            break;
          }
        }
      }
    }
  }

  private enableKeyEvents() {
    this.keyboardListeningEnabled = true;
    window.addEventListener('keydown', this.onKeyEvent);
  }

  private disableKeyEvents() {
    this.keyboardListeningEnabled = false;
    window.removeEventListener('keydown', this.onKeyEvent);
  }

  ngOnDestroy(): void {
    if(this.keyboardListeningEnabled) {
      this.disableKeyEvents();
    }
  }

  onDropMenuClosed() {
    this.viewModel.clearFilter();
  }
}
