    constructor(readonly authenticatedHttp: AuthenticatedHttp) {
    }

    run(applicationId: ApplicationId, screenId: ScreenId, params: Array<[string, string]>, onSuccess: (screenInstanceId: ScreenInstanceId, volatileScreen: Boolean) => void, onError: (error: string) => void) {
      this.authenticatedHttp.post("screen/run", new CreateScreenInstance(Left(applicationId), ApplicationComponentRef.ofScreenId(screenId), params, true), (data: Either<string, ScreenInitResponse>) => {
        const result = Either.copy(data, l => l, r => ScreenInitResponse.copy(r));
        if (result.isRight()) {
          onSuccess(result.getRight().screenInstanceId, result.getRight().volatileScreen);
        } else {
          onError(result.getLeft());
        }
      })
    }

    runWorkingRelease(applicationId: ApplicationId, screenId: ScreenId, params: Array<[string, string]>, onSuccess: (screenInstanceId: ScreenInstanceId, volatileScreen: Boolean) => void, onError: (error: string) => void) {
      this.authenticatedHttp.post("screen/run-working-release", new CreateScreenWorkingReleaseInstance(Left(applicationId), ApplicationComponentRef.ofScreenId(screenId), params), (data: Either<string, ScreenInitResponse>) => {
        const result = Either.copy(data, l => l, r => ScreenInitResponse.copy(r));
        if (result.isRight()) {
          onSuccess(result.getRight().screenInstanceId, result.getRight().volatileScreen);
        } else {
          onError(result.getLeft());
        }
      })
    }

    private instanceCache: Map<string, ScreenInstanceState> = new Map();


    private getComponents(applicationIdentifier: string, screenIdentifier: string, storedScreenReleasesIds: string, onError: (message: string) => void): Promise<ScreenDefinitionComponents> {
      // Components definition is quite large, so we want browser to cache it, thous separate GET request
      return new Promise<ScreenDefinitionComponents>((resolve, reject) => {
        this.authenticatedHttp.get("screen/external-get-screen-components/"+applicationIdentifier+"/"+ screenIdentifier+"?"+storedScreenReleasesIds, (data: Either<string, ScreenDefinitionComponents>) => {
          const result = Either.copy(data, l => l, r => ScreenDefinitionComponents.copy(r));
          if(result.isRight()) {
            resolve(result.getRight());
          } else {
            onError(result.getLeft());
          }
        });
      });
    }

    runExternal(applicationIdentifier: string, screenIdentifier: string, params: Array<[string, string]>,
                onSuccess: (screenInstanceId: ScreenInstanceId, volatileScreen: boolean) => void, onError: (error: string) => void) {

      let storedScreenReleasesIds = localStorage.getItem("screenReleases|"+applicationIdentifier+"|"+screenIdentifier);
      let components: Promise<ScreenDefinitionComponents>|null =
        storedScreenReleasesIds === null ? null : this.getComponents(applicationIdentifier, screenIdentifier, storedScreenReleasesIds, onError);


      this.authenticatedHttp.post("screen/run-external-anonymously-and-get",
        new CreateScreenInstance(Right(applicationIdentifier), ApplicationComponentRef.ofIdentifier(screenIdentifier), params, false), (data: Either<string, ScreenInstanceState>) => {
        const result = Either.copy(data, l => l, r => ScreenInstanceState.copy(r));
        if (result.isRight()) {
          let instance: ScreenInstanceState = result.getRight();

          let allReleases = instance.allScreensReleases.sort().join(",");
          if(components === null || storedScreenReleasesIds !== allReleases) {
            components = this.getComponents(applicationIdentifier, screenIdentifier, allReleases, onError);
            localStorage.setItem("screenReleases|"+applicationIdentifier+"|"+screenIdentifier, allReleases);
          }

          if(components === null ){
            throw new Error("Should not happen");
          } else {
            components.then(data => {
              instance.components = Some(data.components);

              this.instanceCache.set(instance.id.id, instance);

              setTimeout(() => {
                this.instanceCache.delete(instance.id.id);
              }, 10000); // just to not keep it unnecessary

              onSuccess(instance.id, (instance.lifeMode.isVolatile()));
            });
          }


        } else {
          onError(result.getLeft());
        }
      }, (status) => {
        onError("Unable to initialize Screen: " + status);
      })
    }

    changeInputParameters(instanceId: ScreenInstanceId, params: Array<[string, string]>, onSuccess: () => void) {
      this.authenticatedHttp.post("screen/change-input-parameters", new ChangeSimpleInputParameters(instanceId, params), (data: ApplicationResponse) => {
        const copied = ApplicationResponse.copy(data);
        if(copied.isSuccess()) {
          onSuccess();
        } else {
          toastr.error(copied.status);
        }
      })
    }

    getForWidgetScope(widgetInstanceId: WidgetInstanceId, onSuccess: (instance: ScreenInstanceId) => void, onError: (message: string) => void) {
      this.authenticatedHttp.post("screen/get-for-widget-scope", new GetScreenInstanceForWidgetScope(widgetInstanceId), (data: Either<string, ScreenInstanceId>) => {
        const result = Either.copy(data, l => l, r => r);
        if (result.isRight()) {
          onSuccess(result.getRight());
        } else {
          onError(result.getLeft());
        }
      })
    }

    getInstance(id: ScreenInstanceId, onSuccess: (instance: Option<ScreenInstanceState>) => void) {
      const fromCache = this.instanceCache.get(id.id);
      if(fromCache !== undefined) {
        this.instanceCache.delete(id.id);
        onSuccess(Some(fromCache))
      } else {
        this.authenticatedHttp.post("screen/get-instance",new GetScreenInstanceState(id), (data: Option<ScreenInstanceState>) => {
          onSuccess(Option.copy(data).map(aw => ScreenInstanceState.copy(aw)));
        })
      }
    };

