import {
  ComponentViewModelUtils,
  ComponentViewModelWithLabel, CssUtils,
  ScreenContainerViewModel
} from "@screens";
import {I18nText} from "@libs/I18nText";
import {SingleCheckboxComponentRefState, SingleCheckboxComponentState} from "./SingleCheckboxComponentState";
import {ScreenInstanceServerModel} from "@screens";
import {SingleCheckboxComponentDefinition, SingleCheckboxComponentRef} from "./SingleCheckboxComponentDefinition";
import {ScreenSharedViewModel} from "@screens";
import {BooleanVariable} from "@libs/ProcessVariables";
import {VariableId} from "@libs/IdModel";
import {None, Option, Some} from "@libs/Option";
import {randomString} from "@libs/Strings";

export class SingleCheckboxComponentViewModel extends ComponentViewModelWithLabel {

  override typeName = "SingleCheckbox";

  private threeStates: boolean = false;
  public selected: boolean|null = false;
  public tooltip: Option<string> = Option.NoneSingleton;
  public valueLabel: string = "";
  public css: string = "";
  public required: boolean = false;
  public invalid: boolean = false; //TODO

  public requiredIconVisible = false;

  public iconBackgroundColorCss: string|null = null;

  constructor(override readonly shared: ScreenSharedViewModel,
              override readonly parent: ScreenContainerViewModel|null,
              readonly context: VariableId,
              override readonly definition: SingleCheckboxComponentDefinition,
              override readonly componentScreenId: string,
              readonly ref: SingleCheckboxComponentRef,
              override readonly refScreenId: string,
              override readonly componentState: SingleCheckboxComponentState,
              readonly refState: SingleCheckboxComponentRefState,
              readonly serverModel: ScreenInstanceServerModel
  ) {
    super(parent, definition, componentState, refState, shared);
    this.update();
  }

  onChange() {

    if(this.uncoveredAndVisible && this.editable) {

      if (this.threeStates) {
        if (this.selected === true) {
          this.selected = false;
        } else if (this.selected === false) {
          this.selected = null;
        } else {
          this.selected = true;
        }
      } else {
        this.selected = !this.selected;
      }

      if (this.selected === null) {
        this.componentState.updateModel(SingleCheckboxComponentDefinition.MODEL, None());
        this.serverModel.clearModelWithAction(this.componentRefPath(), SingleCheckboxComponentDefinition.MODEL, SingleCheckboxComponentDefinition.ON_CHANGE);
      } else {
        this.componentState.updateModel(SingleCheckboxComponentDefinition.MODEL, Some(new BooleanVariable(this.selected)));
        this.serverModel.changeModelWithAction(this.componentRefPath(), SingleCheckboxComponentDefinition.MODEL, new BooleanVariable(this.selected), SingleCheckboxComponentDefinition.ON_CHANGE);
      }
    }

  }

  updateComponent(deep: boolean): void {


    this.css = ComponentViewModelUtils.toPaddingsCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.paddingsProperties, this.componentState.paddingsState) +
      ComponentViewModelUtils.toBorderCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.bordersProperties, this.componentState.bordersState) +
      ComponentViewModelUtils.toBackgroundCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.backgroundsProperties, this.componentState.backgroundsState) +
      ComponentViewModelUtils.toTextCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.textProperties, this.componentState.textState);

    this.valueLabel = this.definition.valueLabel.currentValue(() => this.componentState.valueLabel).valueOrDefault(I18nText.ofCurrent("!not label!")).getCurrentWithFallback();
    this.tooltip = this.definition.tooltip.currentValue(() => this.componentState.tooltip).valueOrDefault(None()).map(t => t.getCurrentWithFallback());
    this.required = this.ref.required.currentValue(() => this.refState.required).valueOrDefault(false);


    this.threeStates = this.definition.threeStates.currentValue(() => this.componentState.threeStates).valueOrDefault(false);

    this.selected = this.componentState.model.valueOrDefault(None()).getOrElse(<any>(this.threeStates ? null : false));

    this.requiredIconVisible = this.required && (!this.label || !this.label.visible);

    this.cssClass = `SingleCheckboxComponent_${this.id}`;

    this.iconBackgroundColorCss = this.definition.iconBackgroundColor(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider).currentValue(() => this.componentState.iconBackgroundColor).valueOrDefault(None()).getOrNull();


    super.updatePosition();
  }


}
