import {Component, Input} from "@angular/core";
import {LocalDateOptionalTime, LocalDateTime} from "../Types";

@Component({
  selector: 'my-time-summary[time]',
  template: '{{formattedText}}',
  styleUrls: ['./time-summary.component.scss']
})
export class TimeSummaryComponent {
  formattedText: string = "";
  formattedFromNow: string = "";

  @Input() set time(value: LocalDateTime|LocalDateOptionalTime|undefined) {
    if(value === undefined) {
      this.formattedText = "";
      this.formattedFromNow = "";
    } else {
      this.formattedText = value.formattedTimezonedWords();
      this.formattedFromNow = value.formattedFromNow();
    }
  }



}
