import {
  ComponentViewModelUtils,
  FileSource,
  ScreenComponentViewModel,
  ScreenContainerViewModel,
  ScreenInstanceServerModel,
  ScreenSharedViewModel
} from "@screens";
import {FileUri} from "@libs/FileUri";
import {ImageComponentRefState, ImageComponentState} from "./ImageComponentState";
import {ImageComponentDefinition, ImageComponentRef} from "./ImageComponentDefinition";
import {VariableId} from "@libs/IdModel";
import {None} from "@libs/Option";

export class ImageComponentViewModel extends ScreenComponentViewModel {

  override typeName = "Image";

  public fileSource!: FileSource;
  public css: string = "";

  constructor(override readonly shared: ScreenSharedViewModel,
              override readonly parent: ScreenContainerViewModel|null,
              readonly context: VariableId,
              override readonly definition: ImageComponentDefinition,
              override readonly componentScreenId: string,
              readonly ref: ImageComponentRef,
              override readonly refScreenId: string,
              readonly componentState: ImageComponentState,
              readonly refState: ImageComponentRefState,
              readonly serverModel: ScreenInstanceServerModel) {
    super(parent, componentState, refState, definition, shared);
    this.update();
  }


  updateComponent(deep: boolean): void {

    let css = ComponentViewModelUtils.toPaddingsCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider,this.definition.paddingsProperties, this.componentState.paddingsState) +
      ComponentViewModelUtils.toBorderCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.bordersProperties, this.componentState.bordersState) +
      ComponentViewModelUtils.toBackgroundCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.backgroundsProperties, this.componentState.backgroundsState);

    const innerShadow = this.definition.innerShadow(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider).currentValue(() => this.componentState.innerShadow).valueOrDefault(None());
    if(innerShadow.isDefined()) {
      const outerShadowCss = ComponentViewModelUtils.toOuterShadowCss(this.skinName, this.typeName, this.componentClass, this.defaultPropertyProvider, this.definition.bordersProperties, this.componentState.bordersState);
      css += innerShadow.get() ? "box-shadow: "+ outerShadowCss+", inset 0 0.1rem 0.3rem rgba(0, 0, 0, 0.05);" : "box-shadow: "+outerShadowCss+", inset none;";
    }

    const currentValueOption = this.definition.fileSource.currentValue(() => this.componentState.fileSource).valueOrDefault(None());

    this.fileSource = new FileSource("");

    currentValueOption.forEach(currentValue => {
      if(currentValue instanceof FileUri) {
        this.fileSource = new FileSource(currentValue.serialize());
      } else {
        throw new Error("Unexpected current value type");
      }
      if(this.fileSource.fileUri.isRepositoryFile()) {
        this.fileSource.downloadUrl = this.serverModel.getFileDownloadUri(this.fileSource.fileUri);
      } else if(this.fileSource.fileUri.isRepositoryFileId()) {
        this.fileSource.downloadUrl = this.serverModel.getFileDownloadUri(this.fileSource.fileUri);
      } else {
        this.fileSource.downloadUrl = this.fileSource.url;
      }
    });


    super.updatePosition();

    this.css = css + this.sizeCss;
  }


}

