import {Injectable} from "@angular/core";
import {HttpClient} from "@angular/common/http";
import {catchError, map} from "rxjs/operators";
import {Observable, of} from "rxjs";
import {clearArray} from "@libs/Collections";
import {I18nService, Translation} from "../internationalization/i18n.service";


@Injectable({
  providedIn: 'root',
})
export class LibrariesService {

  private static googleMapsObservable: Observable<boolean>|undefined;
  private static googleMapsLoaded = false;
  private static googleMapsCallbacks: Array<() => void> = [];


  private static fineUploaderLoading = false;
  private static fineUploaderLoaded = false;
  private static fineUploaderCallbacks: Array<() => void> = [];


  constructor(private readonly httpClient: HttpClient,
              private readonly i18nService: I18nService) {}

  loadGoogleMaps(onSuccess: () => void): Observable<boolean> {

    if(LibrariesService.googleMapsLoaded) {
      if(LibrariesService.googleMapsObservable !== undefined) {
        onSuccess();
      } else {
        throw new Error("No observable");
      }
    } else {
      LibrariesService.googleMapsCallbacks.push(onSuccess);
      if(LibrariesService.googleMapsObservable === undefined) {
        const apiKey = "AIzaSyD1KQal7N8NRy2CX7QYk0na0kkTihX490I";
        console.log("MAPS API");
        LibrariesService.googleMapsObservable = this.loadLibrary("https://maps.googleapis.com/maps/api/js?key=" + apiKey, this.onGoogleMapsLoaded);
      }
    }
    return LibrariesService.googleMapsObservable;

  }

  private onGoogleMapsLoaded = () => {
    LibrariesService.googleMapsLoaded = true;
    const callbacks = LibrariesService.googleMapsCallbacks.slice();
    clearArray(LibrariesService.googleMapsCallbacks);
    callbacks.forEach(callback => callback());
  };

  loadFineUploader(onSuccess: () => void): void {



    if(LibrariesService.fineUploaderLoaded) {
      onSuccess();
    } else {
      LibrariesService.fineUploaderCallbacks.push(onSuccess);
      if (!LibrariesService.fineUploaderLoading) {
        LibrariesService.fineUploaderLoading = true;

        let translationLoaded = false;
        let libraryLoaded = false;

        this.i18nService.initialize(Translation.fileUploader).finally(() => {
          if(libraryLoaded) {
            this.onFineUploaderLoaded();
          } else {
            translationLoaded = true;
          }
        });

        this.loadLibraryJSONP(["libs/fine-uploader/fine-uploader.min.js"], () => {
          if(translationLoaded) {
            this.onFineUploaderLoaded();
          } else {
            libraryLoaded = true;
          }
        });

      }
    }
  }

  private onFineUploaderLoaded = () => {
    LibrariesService.fineUploaderLoaded = true;
    const callbacks = LibrariesService.fineUploaderCallbacks.slice();
    clearArray(LibrariesService.fineUploaderCallbacks);
    callbacks.forEach(callback => callback());
  };


  private loadLibrary(url: string, onSuccess: () => void): Observable<boolean> {
    return this.httpClient.jsonp(url, "callback").pipe(
        map(() => {
          onSuccess();
          return true;
        }),
        catchError((err, caught) => {
          return of(false)
        }),
      );
  }


  private loadLibraryJSONP(urls: Array<string>, onSuccess: () => void): void {

    let loaded = 0;

    urls.forEach(url => {
      const script = document.createElement("script");
      script.src = url;
      // script.async = true;
      document.head.appendChild(script);

      script.addEventListener("load", () => {
        loaded++;
        if(loaded === urls.length) {
          onSuccess();
        }
      });

    });

  }
}
