import {Component, ElementRef, EventEmitter, Input, OnDestroy, Output, ViewChild} from "@angular/core";
import {
  getElementPositionAndSize,
  getREMSize,
  getSize,
  getVisibleBox,
} from "@libs/Elements";
import {RectXY} from "@libs/RectXY";
import {onFormFocusLost} from "@libs/Dom";

@Component({
  selector: 'my-drop-menu[cssClass]([visible])[anchor]',
  templateUrl: './drop-menu.component.html',
  styleUrls: ['./drop-menu.component.shadow.scss']
})
// Directive annotation is here so compiler would not complain, but it should have no side effects
export class DropMenuComponent implements OnDestroy {

  popupVisible: boolean = false;
  above: boolean = false;

  @Input() cssClass: string|undefined;
  @Input() anchor: HTMLElement|undefined;
  @ViewChild("popupComponent") popupComponent: ElementRef<HTMLElement>|undefined;

  @Output() opened = new EventEmitter<HTMLElement>();
  @Output() closed = new EventEmitter<void>();

  private fixInterval: number = 0;

  private lastPosition!: RectXY;

  private formFocusLostCancel: (() => void)|null = null;

  @Input() set visible(visible: boolean) {
    if(visible) {
      this.showPopup();
    } else if(this.popupVisible) {
      this.hidePopup();
    }
  }

  @Output() visibleChange = new EventEmitter<boolean>();

  private showPopup(): void {

    this.popupVisible = true;
    this.lastPosition = new RectXY(Number.NaN, Number.NaN, Number.NaN, Number.NaN);

    if(this.popupComponent === undefined) {
      throw new Error("No popupComponent");
    } else if(this.anchor === undefined) {
      throw new Error("No anchor");
    } else {
      const dialogOverlay = document.getElementById("drop-menu-overlay");
      if (dialogOverlay == null) {
        throw new Error("Cannot get drop-menu-overlay");
      } else {
        dialogOverlay.classList.add("blockingVisible")
        dialogOverlay.appendChild(this.popupComponent.nativeElement);
      }

      this.opened.emit(this.popupComponent.nativeElement);


      // position now
      requestAnimationFrame(this.fixPosition);
      // position when scroll happen
      document.addEventListener('scroll', this.onScroll, true);
      // position every 0.5 second
      this.fixInterval = <any>setInterval(() => this.fixPosition(), 500);


      this.formFocusLostCancel = onFormFocusLost([this.anchor, this.popupComponent.nativeElement], () => this.hidePopup());

    }
  }


  private onScroll = () => {
    this.fixPosition();
  }

  hidePopup(): void {

    this.popupVisible = false;

    if(this.popupComponent === undefined) {
      throw new Error("No popupComponent");
    } else {
      const dialogOverlay = document.getElementById("drop-menu-overlay");
      if (dialogOverlay == null) {
        throw new Error("Cannot get dialogOverlay");
      } else {
        dialogOverlay.classList.remove("blockingVisible")
        if(dialogOverlay.contains(this.popupComponent.nativeElement)) {
          dialogOverlay.removeChild(this.popupComponent.nativeElement);
        }
      }
    }

    clearInterval(this.fixInterval);
    document.removeEventListener('scroll', this.onScroll, true);

    this.visibleChange.emit(false);
    this.closed.emit();

    if(this.formFocusLostCancel !== null) {
      this.formFocusLostCancel();
    }
  }



  private fixPosition = () => {
    if(this.popupComponent === undefined) {
      throw new Error("No popupComponent")
    } else if (this.anchor === undefined) {
      throw new Error("No container");
    } else {

      const popupElement = this.popupComponent.nativeElement;
      const anchorPosition = getElementPositionAndSize(this.anchor);


      const elementSize = getSize(popupElement);
      const visibleBox = getVisibleBox();
      const remSize = getREMSize();

      const visibleBoxHeight = visibleBox.bottom - visibleBox.top;

      const tooLittleSpaceBottom = anchorPosition.endY + elementSize.height > visibleBox.bottom;

      let spaceAbove = anchorPosition.y - visibleBox.top;
      const spaceBelow = visibleBoxHeight + visibleBox.top - anchorPosition.endY;

      const above = tooLittleSpaceBottom && spaceAbove > spaceBelow;

      const newTop = above
        ? (anchorPosition.y - elementSize.height)
        : anchorPosition.endY;

      const newMaxHeight = above
        ? spaceAbove - Math.floor(remSize / 2)
        : spaceBelow - Math.floor(remSize / 2);

      const newPosition = new RectXY(anchorPosition.x, newTop, anchorPosition.width, newMaxHeight);

      if(!newPosition.equals(this.lastPosition) || this.above !== above) {
        this.above = above;
        this.lastPosition = newPosition;
        popupElement.style.top = newPosition.y+"px";
        popupElement.style.left = newPosition.x+"px";
        popupElement.style.minWidth = newPosition.width+"px";
        popupElement.style.maxHeight = newPosition.height+"px";

        console.log("Setting " + newPosition.y, anchorPosition.y, anchorPosition.endY, elementSize.height, visibleBox.top);
      }

    }

  };


  ngOnDestroy(): void {
    const dialogOverlay = document.getElementById("drop-menu-overlay");
    if(dialogOverlay !== null && this.popupComponent !== undefined) {
      if(dialogOverlay.contains(this.popupComponent.nativeElement)) {
        dialogOverlay.removeChild(this.popupComponent.nativeElement);
      }
    }
  }
}
