import {Component, ElementRef, Input, OnDestroy, ViewChild, ViewContainerRef} from "@angular/core";
import {DateInputComponentViewModel} from "@screens";
import {onFormFocusLost} from "@libs/Dom";

@Component({
  selector: 'my-date-input-component[viewModel]',
  templateUrl: './date-input-component.component.html',
  // styleUrls: ['./date-input-component.component.scss']
})

export class DateInputComponentComponent implements OnDestroy {
  @Input() viewModel!: DateInputComponentViewModel;

  @ViewChild("DateInputComponent") container: ElementRef<HTMLElement>|undefined;
  @ViewChild("dateInputPopup") dropListPopup: ElementRef<HTMLElement>|undefined;

  constructor(private readonly viewContainerRef: ViewContainerRef) {
  }

  ngOnDestroy(): void {

  }

  onDropMenuOpened(popup: HTMLElement) {
    if(this.container == null) {
      throw new Error("No container");
    } else {
      onFormFocusLost([this.container.nativeElement, <HTMLElement>popup], this.viewModel.closePicker);
    }
  }
}

